.site--main {
	.twitter-rotator {
		display: flex;

		.twitter-feed {
			display: flex;

			.tweet-container {
				display: flex;

				.tweet {
					height: 100%;
					padding: 15px;
					margin: 0 15px;
					border: 2px solid @color__brand-blue;
					border-radius: 6px;
					word-break: break-word;
					display: flex;
					flex-wrap: wrap;

					.tweet--header {
						display: flex;
						margin-bottom: 10px;
						width: 100%;
						min-width: 100%;

						.tweet--profile-image {
							width: 36px;
							margin-right: 10px;
						}

						.tweet--profile {
							font-size: @font-size__base;
							color: @color__brand-blue;
							line-height: 1.1;
							padding-right: 30px;
							width: 100%;
							height: auto;
							position: relative;

							.profile--name {
								font-weight: bold;
							}

							.profile--handle {
								font-size: @font-size__base * 0.8;
							}

							&:hover {
								text-decoration: underline;
								color: lighten(@color__brand-blue, 10%);
								opacity: 0.95;
							}

							&:after {
								content: '';
								position: absolute;
								top: 0;
								right: 0;

								.icon__svg-background(
									24px,
									@color__brand-blue,
									@path__icon-twitter,
									1792
								);
							}
						}
					}

					.tweet--body {
						font-size: @font-size__base * 0.88;
						width: 100%;
						display: flex;
						flex-wrap: wrap;

						p {
							font-size: @font-size__base * 0.88;
							margin: 0;
						}

						.tweet--date {
							margin-top: 5px;
						}
					}

					.tweet--footer {
						display: flex;
						flex-wrap: nowrap;
						width: 100%;
						margin-top: 15px;
						align-self: flex-end;

						.tweet--comment,
						.tweet--retweet,
						.tweet--favorite {
							flex: 1;
							text-align: center;
							min-width: 33%;

							a {
								display: inline-block;
								margin: 0 auto;

								.icon {
									width: 20px;
									height: 20px;

									&.icon__svg {
										color: @color__brand-blue;
										fill: @color__brand-blue;
									}
								}
							}
						}

						.tweet--comment {
							text-align: left;
						}

						.tweet--favorite {
							text-align: right;
						}

						.tweet--retweet {
							a {
								.icon {
									width: 30px;
								}
							}
						}
					}
				}
			}
		}
	}
}
