@color__tsvcm-primary: #bae01b; // Bright Green
@color__tsvcm-secondary: #4a76ce; // Light Blue
@color__tsvcm-tertiary: #8e8e8e; // Grey

.tsvcm {
    font-size: @font-size__base;
    // Site Header & Banner
	.site--header {}
		.pane__banner {}

    // Main Body
	.site--main {}
		.section {
            padding: 1.5rem 0;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            background-attachment: fixed;

            &.section__about {}
            &.section__publications {
                background-color: @color__tsvcm-primary;
                background-image: url(./images/section-background-transparent.png);
            }
            &.section__members {}
        }
 
        // Members
        .members-row {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-content: center;
            padding: 1em 0;
        }
        .members-row--member {
            padding: 1em;
            flex: 0 1 calc(33.33% - 3em);
            background: #dcdcdc;
            margin: 1em;
            border-radius: 4px;
            min-width: 280px;
            display: flex;
            align-items: center;
        }

	// Site Footer		
	.site--footer {
        background-color: @color__tsvcm-secondary;
        
        ul.tsvcm-footer-links {
            margin: 0;
            padding: 0;
            text-align: center;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            list-style: none;

            li {
                padding: 1em;
            }
                a {
                    color: #fff;
                    
                    &:hover {
                        text-decoration: underline;
                    }
                }
        }
    }
}