.mx {
    &.mx-wrapper {
        a {
            color: @color__link;

            &:hover {
                color: @color__link__hover;
            }
            &:focus {
                color: @color__link__focus;
            }
            &:active {
                color: @color__link__active;
            }

            &:visited {
                color: @color__link__visited;

                &:hover {
                    color: @color__link__hover;
                }
                &:focus {
                    color: @color__link__focus;
                }
            }
        }

        .btn {
            max-width: none;
        }
        .btn-primary,
        .btn-outline-primary {
            color: #fff !important;
            background-color: #0069d9 !important;
            border-color: #0062cc !important;
        }

        input,
        a {
            &.btn {
                &.btn-primary {
                    color: @color__white !important;
                    background-color: @color__brand-blue !important;
                    border-color: @color__brand-blue !important;
                    font-weight: bold !important;

                    &:focus {
                        box-shadow: none !important;
                    }
                }
                &.btn-outline-primary {
                    background-color: @color__brand-blue !important;
                    border-color: @color__brand-blue !important;
                    font-weight: bold !important;
                }
            }
            &.mx-edit-hyperlink {
                &.btn {
                    &.btn-primary {
                        background-color: @color__brand-blue !important;
                        border-color: @color__brand-blue !important;
                        font-weight: bold !important;
                    }
                }
            }
        }

        .input-group {
            .input-group-append,
            .input-group-prepend {
                input,
                a {
                    &.btn {
                        &.btn-primary {
                            color: @color__white !important;
                            background-color: @color__brand-blue !important;
                            border-color: @color__brand-blue !important;
                            font-weight: bold !important;

                            &:focus {
                                box-shadow: none !important;
                            }
                        }
                    }
                }
            }
        }

        .text-primary {
            color: @color__brand-blue !important;
        }

        .mx-panelbar {
            ul {
                &.rpRootGroup {
                    li {
                        &.rpItem {
                            a {
                                &.rpLink {
                                    color: @color__brand-blue !important;
                                }
                            }
                        }
                    }
                }
            }
        }

        &.mx-global-navigationstatus-wrapper {
            ul {
                &.nav-pills {
                    li {
                        &.nav-item {
                            &.border-primary {
                                border-color: @color__brand-gold !important;
                            }
                            .mx-number-count {
                                &.border-primary {
                                    border-color: @color__brand-gold !important;
                                }
                            }
                            &.active {
                                .mx-number-count {
                                    &.bg-primary {
                                        background: @color__brand-gold !important;
                                    }
                                    &.border-primary {
                                        border-color: @color__brand-gold !important;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        &.mx-shoppingcart-cart-wrapper {
            .mx-shoppingcartwithitems-panel {
                ul {
                    &.nav-pills {
                        li {
                            &.nav-item {
                                &.border-primary {
                                    border-color: @color__brand-gold !important;
                                }
                                .mx-number-count {
                                    &.border-primary {
                                        border-color: @color__brand-gold !important;
                                    }
                                }
                                &.active {
                                    .mx-number-count {
                                        &.bg-primary {
                                            background: @color__brand-gold !important;
                                        }
                                        &.border-primary {
                                            border-color: @color__brand-gold !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        &.mx-meeting-registrationinformation-wrapper {
            fieldset {
                input {
                    &.mx-registration-button {
                        display: block;
                        margin: 0 auto;
                    }
                }
            }
        }
    }
}

.DnnModule-MX-Customer-Addresses {
    .mx {
        .btn-light {
            color: #fff !important;
            background-color: #0069d9 !important;
            border-color: #0062cc !important;
        }
    }
}

.confirmation-module {
    .d-none {
        display: none !important;
    }

    .mx-global-form-wrapper {
        padding: 15px;
        margin: 15px 0;
        border-top: 1px solid #f1d094;
        border-bottom: 1px solid #f1d094;
    }

    .RadPanelBar_Default {
        background: transparent !important;

        .rpGroup {
            background: transparent !important;
            background-color: transparent !important;
        }
    }

    .mx {
        table {
            margin: 0 !important;
        }
    }
    .mx-panelbar {
        ul.rpRootGroup {
            border: 0 !important;

            li.rpItem.rpFirst {
                a.rpLink {
                    display: none !important;
                }
            }
        }
    }
    .form-group {
        &.mx-left-col.col-md-6.d-none {
            display: none !important;
        }
        &.mx-right-col.col-md-6 {
            width: 100% !important;
            text-align: center !important;
            min-width: 100% !important;
        }
    }
}
