// Header Global Nav Menu (desktop styles)

.header--main-nav {
    color: @color__brand-blue;

    ul {}

    li {}

    a {
        color: @color__brand-blue;
        font-size: 1.6em;
        font-weight: 700;
        transition: all 0.25s ease;
        line-height: 1.314;

        &:hover {
            color: @color__brand-blue;
        }
    }
}

.header--navbar {
    &.header--navbar__desktop {
        background-image: url("data:image/svg+xml;base64,PD94bWwgdmVyc2lvbj0iMS4wIiBlbmNvZGluZz0idXRmLTgiP…Igd2lkdGg9IjEwMCUiIGhlaWdodD0iMTAwJSIgZmlsbD0idXJsKCNncmFkKSIgLz48L3N2Zz4g");
        background-size: 100%;
        background-image: -webkit-gradient( linear, 50% 0%, 50% 100%, color-stop(50%, #ffffff), color-stop(100%, rgba(255, 255, 255, 0)) );
        background-image: -moz-linear-gradient( #ffffff 50%, rgba(255, 255, 255, 0) );
        background-image: -webkit-linear-gradient( #ffffff 50%, rgba(255, 255, 255, 0) );
        background-image: linear-gradient(#ffffff 50%, rgba(255, 255, 255, 0));

        .navbar--menus,
        .navbar--utility {
            display: none; //hide DESKTOP navbar on mobile size devices and up

            .header--main-nav {
                .menu {
                    li {
                        &.menu-item__parent {
                            .submenu-toggle {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
}

///////// Media Queries /////////

// 1x Small / Small Tablet, Large Phone
@media screen and (min-width: @breakpoint__1x-small) {}

// Small / Small Tablet
@media screen and (min-width: @breakpoint__small) {}

// Mid-Size / Average Tablet
@media screen and (min-width: @breakpoint__medium) {
    .site--header {
        .header--navbar {
            &.header--navbar__desktop {
                .navbar--menus,
                .navbar--utility {
                    display: block; //show DESKTOP navbar on desktop size devices and up
                }
            }
        }

        .header--main-nav {
            .menu__main {
                overflow-x: visible;

                li {
                    &:hover > .menu__submenu {
                        display: block;
                    }

                    &[data-level='0'] {
                        > a {
                            border-bottom: 4px solid transparent;

                            &:hover {
                                border-bottom-color: @color__brand-gold;
                            }
                        }
                    }
                }
            }

            .menu__submenu {
                display: none;
                position: absolute;
                top: 100%;
                left: auto;
                right: auto;
                background: @color__brand-blue-grey;
                min-width: 16em;
                z-index: 1005;
                white-space: nowrap;

                li {
                    float: none;
                    margin: 0;

                    a {
                        font-size: 1em;
                        font-weight: 300;
                        text-transform: capitalize;
                        margin: 0;
                        padding: 0.75em 2em 0.75em 1em;

                        &:hover {
                            color: @color__brand-gold;
                            background-color: @color__brand-dark-grey;
                            //background-color: rgba(0, 0, 0, 0.125);
                        }
                    }

                    &.menu-item__parent::after {
                        position: absolute;
                        top: 50%;
                        right: 10px;
                        content: '';
                        width: 0;
                        height: 0;
                        border-top: 5px solid transparent;
                        border-bottom: 5px solid transparent;
                        border-left: 10px solid #bcbdc0;
                        transform: translateY(-50%);
                    }

                    &.menu-item__parent::before {
                        width: 0;
                        height: 0;
                    }

                    &::before {
                        width: 0;
                        height: 0;
                    }
                }

                .menu__submenu {
                    top: 0;
                    left: 100%;
                }
            }
        }
    }
}

