.mx {
    &.mx-wrapper {
        &.mx-shoppingcart-cart-wrapper {
            .mx-shoppingcartwithitems-panel {
                ul {
                    &.nav-pills {
                        li {
                            &.nav-item {
                                &.border-primary {
                                    border-color: @color__brand-gold !important;
                                }
                                .mx-number-count {
                                    &.border-primary {
                                        border-color: @color__brand-gold !important;
                                    }
                                }
                                &.active {
                                    .mx-number-count {
                                        &.bg-primary {
                                            background: @color__brand-gold !important;
                                        }
                                        &.border-primary {
                                            border-color: @color__brand-gold !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.custom-mx-form__shopping-cart {
    .card {
        &.selected {
            border-color: #09537d !important;
            box-shadow: 0 0 6px -1px #09537d !important;
        }

        .card-body {
            input {
                &[id*='rptAddresses_btnEdit'] {
                    background: #6c757d !important;
                    border-color: #6c757d !important;
                }
            }
        }
    }

    .mx-navigation {
        input {
            &[type='submit'] {
                &.disabled {
                    background: #ccc !important;
                    border-color: #ccc !important;
                    pointer-events: none;
                }
            }
        }
    }
}
