.Popup-overlay{
    z-index: 999;
}

#huk_cookie_prefernce_panel {
    font-size: 1em;
    width: auto;
    max-width: 500px;
    padding: 2em;

    #COOKIE_CLOSE {
        background-image: none;
        width: 20px;
        height: 20px;

        &::after {
            content: '\f00d';
            font-size: 20px;
            font-family: "Font Awesome 5 Free";
            font-style: normal;
            font-variant-caps: normal;
            font-variant-ligatures: normal;
            font-weight: 900;
            height: 20px;
            line-height: 20px;
            text-rendering: auto;
            width: 20px;
            -webkit-font-smoothing: antialiased;
            color: #bd1537;
        }
    }

    .huk_cookie_prefernce_toolbar {
        padding: 2em 0 1em;
        flex-direction: row;

        input[type='button'] {
            flex: 1;
            width: auto;
            height: auto;
            margin: 0 !important;
            padding: .75em 1.25em !important;
            font-size: 1em !important;
            line-height: 1.314;

            &[value='OK'] {
                margin: 0 1em 0 0;
                flex: 1;
                background: #0b6395 !important;
            } 
        }

        .Popup-checkboxes{
            display: none;
        }

        .Popup-save{
            display: none;
        }
    }
}

@media screen and (max-width: 767px) {

    #huk_cookie_prefernce_panel {
        display: block;
        left: 5%;
        right: 5%;
        transform: translate(0, 0);
    }
}