.accordion-wrapper {
	margin-bottom: 10px;
}
.accordion-container {
	width: 100%;

	h4 {
		margin: 0;
		float: left;
	}

	.accordion-tab {
		width: 100%;
		border: 1px solid #ddd;
		border-top: 0;
		position: relative;
		z-index: 2;
		padding: 5px 10px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
		background: #f5f5f5;

		&:first-child {
			border-top: 1px solid #ccc;
		}

		a {
			color: #fff;
			float: right;
			margin-right: 30px;
		}

		&::after {
			content: '';
			display: table;
			background-size: 100%;
			background-size: cover;
			background-repeat: no-repeat;
			background-position: center;
			height: 35px;
			width: 35px;
			vertical-align: text-bottom;
			background-image: url("data:image/svg+xml,%3Csvg fill='%2309537d' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.59 16.34l4.58-4.59-4.58-4.59L10 5.75l6 6-6 6z'/%3E%3Cpath d='M0-.25h24v24H0z' fill='none'/%3E%3C/svg%3E");
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			transition: transform 0.25s;
		}
	}
	.accordion-tab[aria-expanded='true']::after {
		transform: rotate(90deg) translateX(-50%);
	}

	.accordion-content {
		overflow: hidden;
		transition: max-height 0.25s linear;
		background: @color__white;

		.accordion-content--wrapper {
			padding: 15px;
			border: 1px solid #e7e7e7;
			border-top: 0;

			.mx-items-panel {
				padding: 15px;

				.membership-accordion-info {
					width: 100%;
					margin: 0 0 5px 0;

					.country {
						font-weight: bold;
						padding: 0 0 10px 0;
						font-size: 17px;
						padding-top: 15px;
						display: none;
					}

					&:first-child {
						.country {
							padding-top: 0;
						}
					}

					&.show-country {
						.country {
							display: block;
						}
					}
				}
			}
		}
	}
	.accordion-content[aria-hidden='false'] {
		transition: max-height 0.25s linear;
		width: 100%;
		position: relative;
		z-index: 1;
	}
	.accordion-content[aria-hidden='true'] {
		max-height: 0 !important;
	}
}


// Accordion Component Styles
.accordion {
	width: 100%;

	.accordion--tab {
		width: 100%;
		border-bottom: 1px solid #ccc;
		border-top: 0;
		position: relative;
		z-index: 2;
		padding: 1em;
		cursor: pointer;
		position: relative;
		overflow: hidden;
		color: #4a76ce;
		font-family: Avenir,Lato,Helvetica Neue,Helvetica,Arial,sans-serif;
		font-size: 1.5em;
		font-weight: bold;

		&::after {
			content: '';
			display: table;
			background-size: 100%;
			background-size: cover;
			background-repeat: no-repeat;
			background-position: center;
			height: 35px;
			width: 35px;
			vertical-align: text-bottom;
			background-image: url("data:image/svg+xml,%3Csvg fill='%23555555' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.59 16.34l4.58-4.59-4.58-4.59L10 5.75l6 6-6 6z'/%3E%3Cpath d='M0-.25h24v24H0z' fill='none'/%3E%3C/svg%3E");
			position: absolute;
			right: 5px;
			top: 50%;
			transform: translateY(-50%);
			transition: transform 0.25s;
		}

		&[aria-expanded='true']{
			&::after {
				transform: rotate(90deg) translateX(-50%);
			}
		}
	}

	.accordion--panel {
		padding: 0 1em;
		overflow: hidden;
		transition: max-height 0.25s linear;

		&[aria-hidden='false'] {
			transition: max-height 0.25s linear;
			width: 100%;
			position: relative;
			z-index: 1;
		}
		&[aria-hidden='true'] {
			max-height: 0 !important;
		}
	}
}