.dnnEditState .DNNEmptyPane {
	width: auto;
	position: relative;

	&::before {
		background: #ccc;
		padding: 1.75em 1em;
		color: #000;
		display: block;
		text-align: center;
		font-size: 26px;
		font-weight: bold;
	}
}

//DNN 7.x empty pane styling
.dnnDropEmptyPanes {
	width: auto;
	position: relative;

	&::before {
		background: #ccc;
		padding: 0.75em 1em;
		color: #000;
		display: block;
		text-align: center;
		font-size: 26px;
		font-weight: bold;
	}
}
