// DNN Overrides
.dnn_mact li.actionMenuMove ul li {
    height: auto; //Overrides fixed height on li for moving module in action menu
}

#ControlBar_ControlPanel {
    &,
    * {
        &::before,
        &::after,
        & {
            box-sizing: content-box; // Overrides box-sizing conflict with ControlBar styles (positioning and padding)
        }
    }
}

.console-large img {
    box-sizing: content-box; // Overrides box-sizing conflict with Admin Page Icons
}

.dnnCheckbox .mark {
    background-color: transparent; // Overrides background color on mark, .mark is a class used on dnnCheckbox
}

.iif, .tsvcm {

    /// DNN Overrides ///
    .dnnEditState & .DnnModule {
        opacity: 1; // Overrides opacity on all modules when in Edit Mode
    }

    .DNNEmptyPane {
        display: none; // Hides any panes without modules loaded in (based on DNNEmptyPane class)
    }

    .DNNEmptyPane.dnnSortable {
        min-height: 0 !important; // Overrides Minheight for draggable panes when in edit mode
        width: 100%;
        margin-bottom: 10px;
    }

    .site--main {
        .DNNEmptyPane.dnnSortable {
            min-height: auto !important;
            width: 100%;
            margin-bottom: 10px;
        }
    }

    .dnnViewSource textarea,
    .dnnSQLModule textarea,
    .ssStylesheetEditor textarea,
    .dnnAdvancedSettings textarea[name$=PageHeadText] {
        font-family: Consolas, Inconsolata, monospace;
    }

    .dnnFormItem {
        input[type="text"],
        select,
        textarea,
        input[type="email"],
        input[type="search"],
        input[type="password"] {
            padding: 0.5em;
        }
    }

    .dnnActions li,
    ul.dnnAdminTabNav li {
        list-style: none; // Fixes DNN Default override issue
    }

    .DnnModule-Console h3 {
        // make dnn console headers look different if you want
    }

    div.actionMenu ul.dnn_mact > li.actionMenuMove > ul li {
        height: auto;
    }
}
