.site--header {
	margin-bottom: 30px;

	.container {
		padding: 0 !important; //remove row padding
		.container(@site-width);
	}
}
.header--top-bar {
	background-color: @color__brand-deep-blue;
	padding-top: 1em;
	padding-bottom: 0;
	color: @color__white;
	min-height: 5.5em; // min-height for mobile button

	.row__sign-in {
		display: flex;
		margin: 0;
	}

	.sign-in--register {
		display: none;
		flex: 1;
		align-items: center;
		width: 100%;

		.iif__logged-in & {
			display: none !important;
		}

		p {
			margin: 10px 0;
		}

		a {
			color: #9dbacb;
			text-decoration: underline;
		}
	}
	.sign-in--login {
		display: flex;
		justify-content: flex-end;
		flex: 2 !important;
		flex-wrap: wrap;
		padding-bottom: 15px;

		.module-container {
			margin: 0 !important;
		}

		.mx-login-mode-wrapper,
		.mx-anonymous-mod-wrapper {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: flex-end;

			.mx-welcome-back-label {
				margin-right: 10px;
				color: @color__white !important;
				font-weight: normal;

				h6 {
					color: @color__white !important;
					font-weight: normal;
				}
			}

			.mx-login-mode-ul {
				margin: 0;
				list-style: none;
				display: flex;
				flex-wrap: wrap;
				justify-content: flex-end;

				.mx-li {
					text-transform: uppercase;
					color: @color__white;
					font-size: 12px;
					background: #0b6395;
					padding: 0.75em 2em;
					display: inline-block;
					font-weight: 900;
					line-height: 1;
					margin: 0.125em;

					a {
						color: @color__white !important;
					}
				}
			}

			.mx-actions-fieldset {
				.mx-row {
					display: flex;
					flex-wrap: wrap;
					justify-content: flex-end;
				}
				.mx-sign-in-span,
				.mx-create-new-user-account-button,
				.mx-signin-help-button {
					text-transform: uppercase;
					color: @color__white;
					font-size: 12px;
					background: #0b6395;
					padding: 0.75em 2em;
					display: inline-block;
					font-weight: 900;
					line-height: 1;
					margin: 0.125em;
				}
				.mx-sign-in-button {
					color: @color__white !important;
				}
			}
		}

		.mx-loginviewmodeanonymous-panel,
		.mx-loginviewmodeloggedin-panel {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: flex-end;
			text-align: right;

			.mx-welcomeback-span {
				display: none;
			}

			.mx-signin-hyperlink,
			.mx-createnewuseraccount-hyperlink,
			.mx-signinhelp-hyperlink,
			.mx-myprofile-hyperlink,
			.mx-changepassword-hyperlink,
			#dnn_ctr1025_Welcome_ctl00_LoginStatus,
			#dnn_ctr1035_Welcome_ctl00_LoginStatus {
				text-transform: uppercase;
				color: @color__white;
				font-size: 12px;
				background: #0b6395;
				padding: 0.75em 1em;
				display: inline-block;
				font-weight: 900;
				line-height: 1;
				margin: 0.125em;
			}

			a[id$='LoginStatus'] {
				text-transform: uppercase;
				color: @color__white;
				font-size: 12px;
				background: #0b6395;
				padding: 0.75em 1em;
				display: inline-block;
				font-weight: 900;
				line-height: 1;
				margin: 0.125em;
			}
		}

		#Login {
			text-align: right;

			.mx-row {
				a {
					color: #9dbacb;
					margin-left: 10px;
				}
			}

			.module-container {
				margin: 0;
			}
		}
		.login--link-list {
			float: right;
			list-style: none;
			margin: 0;

			li {
				display: inline-block;
			}
		}

		.login--name {
			margin-right: 0.5em;
		}
		.login--link {
			text-transform: uppercase;
			color: @color__white;
			font-size: 12px;
			background: lighten(@color__brand-blue, 5%);
			padding: 0.75em 2em;
			margin: 0 0.25em;
			display: inline-block;
			font-weight: 900;
			line-height: 1;
			margin: 0.125em 0;
		}
		.iif:not(.iif__logged-in) & {
			#Login,
			#dnn_LoginPane {
				display: flex;
			}
			#dnn_LoggedInPane {
				display: none;
			}
			.login--link-list {
				li {
					display: none;
				}
			}
			.login--link {
				display: none;
			}
		}

		.dnnEditState .iif & {
			#Login,
			#dnn_LoginPane,
			#dnn_LoggedInPane {
				display: flex !important;
			}
		}

		.mx-security-signin-wrapper {
			fieldset {
				.form-group {
					.mx-username-label,
					.mx-password-label {
						transition: all 0.25s ease;

						&.label__above {
							top: -22px !important;
							margin-bottom: 0 !important;
						}
					}
				}
			}
		}
	}

	.mx {
		&.mx-security-signin-wrapper {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin: 0;

			.mx-title-wrapper {
				flex: 1 0 auto;
				padding-right: 10px;
				min-width: 100%;
				text-align: left;
				margin-bottom: 25px;

				h3 {
					margin: 0;
					font-size: 16px;
					font-weight: bold;
				}
			}

			fieldset {
				display: block;
				min-width: 100%;

				.form-group {
					position: relative;
					display: inline-block;
					float: left;
					margin: 0;
					width: 100%;
					max-width: 49%;
					margin: 0 0 10px 2%;

					&:first-child {
						margin-left: 0;
					}

					label {
						position: absolute;
						top: 10px;
						left: 0.5rem;
						pointer-events: none;
					}
					input {
						min-width: 0;
						margin: 0;
						background: #014171;
						border: 0;
						color: #fff;
						padding: 0.375rem 0.5rem;
					}
				}

				.mx-signin-button {
					-webkit-appearance: button;
					text-transform: uppercase;
					color: #fff !important;
					font-size: 12px !important;
					background: #0b6395 !important;
					padding: 0.75em 1em !important;
					display: inline-block !important;
					font-weight: 900 !important;
					margin: 0 !important;
					border: 0 !important;
					width: 100% !important;
					max-width: none !important;
				}
				input {
					&.btn {
						&.btn-primary {
							background: #0b6395 !important;
						}
					}
				}
			}

			.mx-rememberme-row {
				display: none !important;
			}

			.mx-hypcreatenewuser-hyperlink.d-block {
				display: none !important;
			}

			.mx-hypsigninhelp-hyperlink {
				border-radius: 0.25rem;
				text-transform: uppercase;
				color: #fff !important;
				font-size: 12px !important;
				background: #0b6395 !important;
				padding: 0.75em 1em !important;
				display: inline-block !important;
				font-weight: 900 !important;
				margin: 0.125em 0 !important;
				border: 0 !important;
				width: 100% !important;
				text-align: center;
				margin-top: 10px !important;
			}
		}
	}
}

.header--navbar {
	padding: 0;

	.row {
		justify-content: space-between;
	}

	.header--top-nav,
	.header--search,
	.header--main-nav {
		display: flex;
	}
	.navbar--logo {
		width: 100%;
		text-align: center;
		border-bottom: 1px solid @color__brand-blue-grey;

		img {
			max-width: 5em;
		}
	}
}

.header--search {
	padding: 1em 0 2em 0;
	position: relative;

	.module-container {
		margin-bottom: 0 !important;
	}

	.searchInputContainer {
		margin: 0 !important;

		a.dnnSearchBoxClearText.dnnShow {
			z-index: 1000;
		}
	}

	input {
		border-radius: 0.25em;
		border: 1px solid @color__brand-blue;
		padding: 0.75em;
		min-width: 0;
		width: 100%;
		font-size: 1em;
	}

	a.SkinObject {
		display: inline-block;
		text-indent: -9999px;
		height: 3em;
		width: 3em;
		padding: 0.75em 0;
		background-image: url(./icons/ic_search_gold.svg);
		background-position: center;
		background-repeat: no-repeat;
		position: absolute;
		top: 14px;
		right: 0;
		z-index: 10;

		&:hover {
			background-image: url(./icons/ic_search_blue.svg);
		}
	}

	#dnn_SearchPaneMobile {
		padding: 0;

		.row {
			padding: 0;
		}

		.col-md-12 {
			padding: 0 15px;
		}
	}

	#dnn_SearchPaneDesktop {
		.row {
			margin: 0;
		}
		.col-md-12 {
			padding: 0;
		}
	}

	#dnn_SearchPaneDesktop,
	#dnn_SearchPaneMobile {
		width: 100%;

		.col-md-12 {
			width: 100%;
		}

		.adv-search {
			width: 100%;
			max-width: 100% !important;
			position: relative;

			.twitter-typeahead {
				height: 42px;

				.form-control {
					border-radius: 0.25em;
					border: 1px solid @color__brand-blue;
					padding: 0.75em;
					min-width: 0;
					width: 100%;
					font-size: 1em;
					height: 42px;
				}
			}

			.input-group-btn {
				position: absolute;
				right: 0;
				top: 0;
				bottom: 0;
				height: 100%;
				display: block;
				width: auto;

				.btn-group {
					height: 100%;

					.search-btn {
						background: transparent;
						border: 0;
						box-shadow: none;

						.glyphicon {
							color: @color__brand-gold;
							font-size: 18px;
							top: 2px;
						}
					}
				}

				&:hover {
					.btn-group {
						.search-btn {
							.glyphicon {
								color: @color__brand-blue;
							}
						}
					}
				}
			}
		}
	}
}

.iif {
	&.iif__logged-in {
		.header--top-bar {
			.sign-in--login {
				#dnn_LoginPane {
					display: none;
				}
			}
		}
	}
}

