///////// Media Queries /////////

// 1x Small / Small Tablet, Large Phone
@media screen and (min-width: @breakpoint__1x-small) {
}

// Small / Small Tablet
@media screen and (min-width: @breakpoint__small) {
	.row__even-columns .row--column {
		flex-basis: 0;
		flex-grow: 1;
	}
}

// Mid-Size / Average Tablet
@media screen and (min-width: @breakpoint__medium) {
	.site--header {
		margin-bottom: 0;

		.header--top-bar {
			padding-bottom: 1em;
			display: flex;
			align-items: center;

			.sign-in--register {
				display: flex;
			}
			.sign-in--login {
				padding-bottom: 0;
			}

			#MobileNavbar {
				width: 100%;
			}

			.mx-security-signin-wrapper {
				flex-wrap: wrap;
				justify-content: flex-end;

				.mx-title-wrapper {
					min-width: 65px;
					max-width: 65px;
					text-align: right;
					margin-bottom: 0;
				}

				fieldset {
					min-width: 0;

					.form-group {
						max-width: 50%;
						padding-right: 5px;
						margin: 0;
						width: auto;
					}
					.mx-signin-button {
						margin: 0 5px 0 0 !important;
						width: auto !important;
					}
				}

				.mx-hypsigninhelp-hyperlink {
					width: auto !important;
					margin-top: 0 !important;
					margin-bottom: 0 !important;
				}
			}
		}
		.header--navbar {
			padding: 0.667em 0 2em;

			.row {
				margin: 0;
			}

			.header--top-nav,
			.header--search,
			.header--main-nav {
				display: flex;
				justify-content: flex-end;
			}
			.navbar--logo {
				width: 100%;
				text-align: left;
				border-bottom: none;

				img {
					max-width: 100%;
				}
			}
			.navbar--utility,
			.navbar--menus {
				width: 100%;
			}
		}

		.header--search {
			padding-top: 0;

			> span {
				width: 100%;
				overflow: hidden;
			}

			.searchInputContainer {
				float: right;
				min-width: 75%;
			}

			a.SkinObject {
				top: 0;
				font-size: 1.125em;
			}

			input {
				font-size: 1.25em;
				width: 100%;
				border-radius: 0.5em;
				border-color: lighten(@color__brand-dark-grey, 20%);
			}

			#dnn_SearchPaneDesktop,
			#dnn_SearchPaneMobile {
				.adv-search {
					.twitter-typeahead {
						height: 55px;

						.form-control {
							font-size: 1.25em;
							border-radius: 0.5em;
							border-color: #bdbdbd;
							height: 55px;
						}
					}
				}
			}
		}
	}
}

// Large / Desktop
@media screen and (min-width: @breakpoint__large) {
	.site--header {
		.header--top-bar {
			padding-bottom: 1em;
		}
		.header--navbar {
			.navbar--logo {
				width: auto;
				flex: 1;
			}
			.navbar--utility {
				width: auto;
				flex: 3;
				padding-top: 2em;
			}
			.navbar--menus {
				width: 100%;
			}
		}
	}
}

