.featured-content {
	display: flex;
	flex-wrap: wrap;
	overflow: hidden;

	.featured-content--item {
		flex: 1 1 340px;
		padding: 0 30px;
		margin: -1px;
		border-left: 1px solid @color__brand-gold;
		border-bottom: 1px solid @color__brand-gold;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		.item--content-support {
			flex: 0 0 100px;
			padding: 30px 30px 30px 0;

			.content-support--icon {
				text-align: center;

				img {
					width: auto;
					height: auto;
					max-width: 80px;
					max-height: 80px;
				}
			}

			.content-support--button {
				text-align: center;
				padding-top: 15px;

				a {
					color: @color__white;
					padding: 0.5em 2em;
					transition: all 0.25s ease;
					background-color: #942f28;
					font-weight: 700;
					text-transform: uppercase;
					text-align: center;
					margin: 0 auto;
					border-radius: 16px;
					font-size: 12px;
					white-space: nowrap;
					display: block;
				}
			}
		}

		.item--text {
			flex: 1 1 250px;
			line-height: 1.314;
			padding: 30px 0;
		}
	}
}
