.dnndeveloper-modal {
	position: relative;
	border-top: 40px solid @color__brand-deep-blue;
	z-index: 1000;
	overflow: visible !important;
	max-width: 750px !important;

	[class*='close-'] {
		position: absolute;
		top: -32px;
		color: red;
		z-index: 1000;
		text-shadow: 0 1px 0 red;
		right: 10px;
		font-size: 20px;
	}
	.close-thik:hover {
		color: red !important;
	}
}
