/// Media Utilities ///
.img-responsive {
    width: 100%;
}

.img__left {
    float: left;
    margin: 0.75em 0.75em 0.75em 0;
}

.img__right {
    float: right;
    margin: 0.75em 0 0.75em 0.75em;
}

.responsive-media {
    max-width: 100%;
    overflow: hidden;
    padding-bottom: percentage(9/16); // 16:9 aspect ratio
    position: relative;

    iframe,
    object,
    embed {
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
}

