.site--main {
    .icon-navigation {
        ul {
            margin: 0;
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;

            li {
                flex: 0 1 280px;
                display: flex;
                flex-wrap: wrap;
                margin: 1em 0;

                a {
                    display: block;
                    margin: 0 auto;
                    text-align: center;
                    padding: 15px;
                    font-size: 24px;
                    color: @color__brand-blue !important;
                    line-height: 1.314;
                    font-weight: bold;

                    .nav-icon {
                        margin: 0 auto 15px auto;
                        text-align: center;
                        height: 100px;
                        display: block;
                        width: 100px;
                        position: relative;

                        img {
                            display: inline-block;
                            height: auto;
                            margin: 0 auto;
                            max-width: 60px;
                            max-height: 60px;
                            position: absolute;
                            top: 50%;
                            transform: translate(-50%, -50%);
                        }

                        &:after {
                            content: '';
                            position: absolute;
                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                            top: 0;
                            left: 0;
                            background: transparent;
                            border: 4px solid transparent;
                            transition: all 0.15s ease;
                        }
                    }

                    &:hover {
                        .nav-icon {
                            &:after {
                                border: 4px solid #e5a73c;
                            }
                        }
                    }
                }
            }
        }

        &.icon-navigation__unlimited {
            ul {
                li {
                    flex: 0 0 auto;

                    a {
                        padding: 30px;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: @breakpoint__medium) {
    .site--main {
        .icon-navigation {
            ul {
                justify-content: flex-start;
                li {
                    flex: 0 1 33.33%;
                }
            }
            &.icon-navigation__unlimited {
                ul {
                    justify-content: center;
                    li {
                        flex: 0 0 auto;
                    }
                }
            }
        }
    }
}
