.anchors {
	background: #efefef;
	padding: 15px;

	.anchor-title {
		font-size: 20px;
		margin-bottom: 30px;
		color: @color__brand-blue;
	}
	ul {
		margin: 0;
		padding: 0 3%;
		list-style: none;

		li {
			&.anchor-link {
				margin-bottom: 5px;

				a {
					color: @color__brand-blue;
					padding-left: 23px;
					text-decoration: underline;
					position: relative;
					font-size: 18px;

					.fa,
					.fas {
						color: @color__brand-gold;
						position: absolute;
						left: 0;
						font-size: 15px;
						top: 50%;
						transform: translateY(-50%);
					}
				}
			}
		}
	}
}
