.featured-speakers {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;

	.featured-speakers--speaker {
		flex: 1 1 auto;
		margin: 2em 15px;
		min-width: 280px;
		text-align: center;

		.speaker--image {
			img {
				overflow: hidden;
				border-radius: 50%;
				max-width: 120px;
			}
		}
		.speaker--name {
			margin-top: 15px;
		}
		.speaker--title {
			margin-top: 5px;
		}
	}

	.featured-speakers--view-all {
		font-size: 20px;
		color: #7f7f7f;
		margin-top: 3em;
		text-align: center;
		line-height: 1.314;
		display: block;
		min-width: 100%;

		a {
			color: #7f7f7f;
			text-decoration: underline;
		}
	}
}
