.clearfix() {
    &::after,
    &::before {
        content: '';
        display: table;
        clear: both;
    }
}

// Hide on page but still accessible to screen readers
.screen-reader-only() {
    position: absolute;
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
    border: 0 none;
    clip: rect(0, 0, 0, 0);
    overflow: hidden;
}

.button-styles(@button-variant:primary) {
    @button-color: "color__button__@{button-variant}";
    @button__hover: "@{button-color}__hover";
    @button__active: "@{button-color}__active";
    display: inline-block;
    cursor: pointer;
    color: @color__white;
    background: @@button-color;
    text-align: center;
    border-radius: 0.25em;
    padding: 0.75em 1em;
    min-width: 7em;
    width: auto;
    line-height: @line-height__base;
    text-shadow: none;
    box-shadow: none;
    border: none;
    transition: background-color @timing, border-color @timing, color @timing;

    &:focus,
    &:hover {
        color: @color__white;
        background: @@button__hover;
    }

    &:active {
        background: @@button__active;
        transition: background-color @timing__fast;
        box-shadow: inset 0 1px 5px 0 fade(@color__black, 40%);
    }
}

.button-styles(@button-variant) when (@button-variant = link) {
    color: @color__text;

    &:focus,
    &:hover {
        color: @color__text;
    }
}

.horizontal-list(@divider:'|'; @spacing: 1.25em) {
    margin: 0;
    overflow-x: hidden;
    padding-bottom: 1px; // fixes overflow scrollbar at some font sizes
    .clearfix();

    li {
        float: left;
        position: relative;
        margin: 0 @spacing 0 0;
        list-style: none;

        &:last-child {
            margin-right: 0;
        }
    }

    > li {
        &::before {
            content: @divider;
            width: @spacing;
            text-align: center;
            position: absolute;
            right: 100%;
        }

        &:first-child::before {
            content: none;
        }
    }
}

.svg-icon(@icon-size, @icon-color) {
    width: @icon-size;
    height: @icon-size;
    fill: @icon-color;
}

.icon__svg-background(@iconSize, @iconFill, @svgPath, @viewboxSize) {
    content: '';
    display: table;
    background-size: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center 1px;
    height: @iconSize;
    width: @iconSize;
    vertical-align: text-bottom;
    @fill: @iconFill;
    @mimetype: 'data:image/svg+xml;charset=utf-8';
    @svgIcon: '<svg xmlns="http://www.w3.org/2000/svg" fill="@{fill}" viewBox="0 0 @{viewboxSize} @{viewboxSize}">@{svgPath}</svg>';
    @svgIconEscaped: escape(@svgIcon);
    background-image: url('@{mimetype},@{svgIconEscaped}');
}
