.iif {
    .site--main {
        .news--feed,
        .news--latest {
            .feed--article {
                &.In_Members_Role {
                    .MemberSecureArticle {
                        display: block;
                    }

                    .NonMemberSecureArticle {
                        display: none;
                    }
                }

                .MemberSecureArticle {
                    display: none;
                }
            }
        }

        .news {
            margin-bottom: 30px;
            position: relative;

            &.news__latest {
                margin-top: 4em;
                margin-bottom: 5em;
                border-top: 1px solid lighten(@color__brand-gold, 20%);

                &::before {
                    content: 'Latest from the IIF';
                    color: @color__brand-deep-blue;
                    font-size: 2.25em;
                    line-height: 1;
                    width: auto;
                    display: table;
                    text-align: center;
                    margin-left: auto;
                    margin-right: auto;
                    margin-top: -0.75em;
                    padding: 0.25em 1em;
                    text-transform: uppercase;
                    background-color: @color__white;
                }

                .news--advocacy {
                    border-top: none;
                    border-bottom: none;
                }

                .container {
                    padding-top: 4em;
                }

                .row {
                    margin: 0;
                    /*
                    margin-left: -(@grid-gutter-width);
                    margin-right: -(@grid-gutter-width);
                    */
                }

                .row--column {
                    flex-basis: 100%;
                    padding: 3em @grid-gutter-width;
                    min-width: 300px;
                }

                .module-container {
                    margin: 0;
                }
            }

            .news--latest {
                .category--title {
                    text-align: center;
                    font-size: 1.5em;
                    font-weight: 900;
                    color: @color__brand-deep-blue;
                    text-transform: uppercase;
                    margin-bottom: 1em;
                    margin-top: 1.5em;
                    position: relative;

                    &::before {
                        content: '';
                        display: block;
                        position: absolute;
                        left: 0;
                        right: 0;
                        bottom: 100%;
                        margin: 0 auto 0.666em;
                        width: 2em;
                        height: 2em;
                        background-repeat: no-repeat;
                        background-size: auto;
                        background-size: contain;
                        background-position: center;
                    }
                }

                .category__research {
                    .category--title {
                        &::before {
                            background-image: url('./icons/icon__news-research.png');
                        }
                    }
                }

                .category__advocacy {
                    .category--title {
                        &::before {
                            background-image: url('./icons/icon__news-advocacy.png');
                        }
                    }
                }

                .category__innovation {
                    .category--title {
                        &::before {
                            background-image: url('./icons/icon__news-innovation.png');
                        }
                    }
                }

                .category--post {
                    display: block;
                    background-color: #f0f0f0;
                    text-decoration: none;
                    padding: 1em 1.25em 0.25em;
                    border-radius: 1em;
                    transition: background-color @timing ease,
                        box-shadow @timing ease;
                    margin-bottom: 2em;

                    &:last-of-type {
                        margin-bottom: 1.5em;
                    }

                    .post--title {
                        margin: 0;
                        font-size: 1.125em;
                        font-weight: 900;
                        line-height: 1.25;
                    }

                    .post--date {
                        margin-bottom: 1em;
                        display: block;
                        font-size: 0.875em;
                    }

                    .post--text {
                        line-height: 1.314;
                        margin-bottom: 18px;

                        br {
                            display: none;
                        }

                        p {
                            margin-bottom: 0;
                        }

                        * {
                            font-size: 1em !important;
                            font-family: @font__sans-serif !important;
                        }
                    }

                    .post--date,
                    .post--text,
                    .post--title {
                        color: @color__brand-deep-blue;
                        transition: color @timing ease;
                    }

                    &:hover {
                        background-color: @color__brand-deep-blue !important;
                        box-shadow: 0.666em 0.666em 0.333em 0
                            fade(@color__black, 20%);

                        .post--date,
                        .post--text,
                        .post--title {
                            color: @color__white;
                        }
                    }
                }

                .category--see-all {
                    display: table;
                    margin: 0 auto -1.25em;
                    font-size: 1.125em;
                    color: #9a004d;
                    text-transform: uppercase;
                    text-align: center;
                }
            }

            .news--feed {
                position: relative;

                .feed--article {
                    background: #f0f0f0;
                    padding: 0 45px 20px;
                    color: #444;
                    margin-bottom: 20px;
                    position: relative;
                    transition: all 0.05s ease;
                    border-radius: 1em;

                    &.article__public {
                        &::after {
                            content: 'Public';
                            display: block;
                            position: absolute;
                            right: 1em;
                            top: 1em;
                            min-width: 10px;
                            padding: 3px 7px;
                            font-size: 12px;
                            font-weight: bold;
                            line-height: 1;
                            white-space: nowrap;
                            text-align: center;
                            vertical-align: middle;
                            border-radius: 10px;
                            color: #fff;
                            background-color: #999999;
                        }
                    }

                    a {
                        color: @color__white;
                    }

                    .article--edit {
                        position: absolute;
                        left: 0;
                        top: 0;

                        a {
                            padding: 0.25em;
                        }
                    }

                    h3.article--title {
                        font-size: @font-size__base * @font-factor__h4;
                        line-height: 1.314;
                        margin: 0;
                        padding-top: 15px;
                    }

                    .article--date {
                        display: block;
                        font-size: 85%;
                        font-style: italic;
                        color: #052d6e !important;
                    }

                    .article--content {
                        color: #052d6e !important;
                        > p {
                            color: #052d6e !important;
                            margin-bottom: 0;

                            * {
                                font-size: 1em !important;
                                font-family: @font__sans-serif !important;
                            }
                        }
                    }

                    .feed--article {
                        padding: 0;
                        margin: 0;
                    }

                    &:hover {
                        background-color: #004172;
                        box-shadow: 7px 7px 9px 0 rgba(189, 190, 191, 0.75);

                        h3 {
                            color: @color__white;
                        }

                        .article--date {
                            color: @color__white !important;
                        }

                        .article--content {
                            color: @color__white !important;
                            > p {
                                color: @color__white !important;
                            }
                        }

                        .feed--article {
                            background-color: #004172;
                            box-shadow: none;
                            padding: 0;
                            margin: 0;

                            .article--date {
                                color: @color__white !important;
                            }

                            .article--content {
                                color: @color__white !important;
                                > p {
                                    color: @color__white !important;
                                }
                            }

                            &:hover {
                                background-color: #004172;
                                box-shadow: none;
                            }
                        }
                    }
                }

                .feed--news-article {
                    background: transparent;
                    padding: 0;
                    color: #555;
                    margin: 0;
                    transition: all 0.05s ease;
                    border-bottom: 1px solid #dedede;
                    padding: 3em 0;

                    a {
                        color: @color__brand-blue;
                    }

                    h2.news-article--title {
                        font-size: 24px;
                        color: #555;
                        font-weight: normal;
                        line-height: 1.314;
                        margin-top: 0;

                        &:hover {
                            text-decoration: underline;
                            color: #477e9e;
                        }
                    }

                    .news-article--info {
                        .news-article--content {
                            color: #777;
                            line-height: @line-height__base;
                        }
                    }
                }
            }

            .news--all {
                text-align: center;

                a {
                    color: @color__brand-blue;
                    font-weight: bold;
                    transition: all 0.25s ease;

                    &:hover {
                        text-decoration: underline;
                        color: lighten(@color__brand-blue, 10%);
                        opacity: 0.95;
                    }
                }
            }
        }

        .row {
            .row--column:nth-child(2) {
                .news--feed:before {
                    content: '';
                    height: 90%;
                    width: 1px;
                    background: @color__brand-gold;
                    position: absolute;
                    left: -16px;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }

        .DnnModule a[href$='/Manage-Publication-Authors'] {
            .btn;
            font-size: 0.8em;
        }

        .NewsArticles {
            font-size: 16px;
            line-height: 1.25;
            color: #4d4d4d;

            .article {
                font-size: 14px;
                color: #888;

                h1,
                h1 a {
                    color: @color__brand-blue;
                    margin-bottom: 5px;

                    &:hover {
                        color: @color__brand-blue;
                    }
                }
            }

            h1,
            h1 a {
                color: @color__brand-blue;
                margin-bottom: 5px;

                &:hover {
                    color: @color__brand-blue;
                }
            }

            .articleEntry {
                margin: 1em 0 4em;
                font-size: 16px;
                line-height: 1.66;

                img {
                    max-width: 100% !important;
                    height: auto !important;
                }

                #page-header {
                    display: none;
                }

                .element-invisible {
                    display: block !important;

                    ul {
                        margin: 0;
                        list-style: none;
                    }

                    a {
                        display: block !important;
                        color: #fff;
                        padding: 1em 1.5em;
                        transition: all 0.25s ease;
                        background-color: #942f28;
                        font-weight: bold;
                        text-transform: uppercase;
                        text-align: center;
                        margin: 3em auto;
                        max-width: 250px;
                        display: block;
                        box-shadow: rgba(0, 0, 0, 0.25) 5px 5px 15px 0;
                        border-radius: 6px;
                        overflow: hidden;
                        position: relative;
                        font-size: 15px !important;
                        line-height: 1 !important;
                        text-indent: -3000px;

                        &::before {
                            content: 'Download PDF';
                            position: absolute;
                            left: 0;
                            top: 0;
                            width: 100%;
                            height: 100%;
                            display: block;
                            text-indent: 0;
                            line-height: 3;
                        }

                        & + span {
                            display: none;
                        }
                    }
                }

                .container,
                .container-fluid {
                    padding: 0;

                    .row {
                        margin: 0;
                    }

                    .content-wrapper {
                        .row {
                            &.sidebar-layout {
                                margin-left: -15px;
                                margin-right: -15px;

                                .panel-panel {
                                    &.col-md-8 {
                                        padding: 0 15px;
                                    }

                                    &.col-md-4 {
                                        padding: 0 15px;
                                    }

                                    strong {
                                        &::after,
                                        &::before {
                                            content: ' ';
                                        }
                                    }

                                    h3 {
                                        font-size: 1.125em;
                                        text-transform: uppercase;
                                    }

                                    .node-person-record {
                                        h3 {
                                            display: none;
                                        }

                                        .group-full-name {
                                            display: table;
                                            margin: 0 auto;
                                            color: #09537d !important;
                                            pointer-events: none;

                                            & > .field {
                                                display: inline-block;
                                                margin: 0.125em !important;

                                                & > .field-items {
                                                    margin-left: 0 !important;
                                                    margin-right: 0 !important;
                                                }
                                            }
                                        }

                                        .field-item {
                                            font-size: 0.875em;
                                            line-height: 1.333;
                                        }
                                    }
                                }

                                .sidebar-layout {
                                    margin: 0;

                                    .col-md-4,
                                    .col-md-8 {
                                        width: 100%;
                                        padding: 0;
                                    }
                                }
                            }
                        }
                    }
                }

                .btn,
                a.btn {
                    color: #fff;
                    padding: 0.75em 1.5em;
                    transition: all 0.25s ease;
                    background-color: #942f28;
                    font-weight: bold;
                    text-transform: uppercase;
                    text-align: center;
                    margin: 3em auto;
                    max-width: 250px;
                    display: block;
                    box-shadow: rgba(0, 0, 0, 0.25) 5px 5px 15px 0;
                    border-radius: 6px;
                }

                .file {
                    .file-icon {
                        display: none;
                    }

                    a {
                        display: none;
                    }

                    .file-size {
                        display: none;
                    }
                }

                img {
                    max-width: 100%;
                }
            }

            .articleCategories {
                border: 0;
            }
            .articleTags {
                border: 0;
                margin-bottom: 2px;
                padding-bottom: 2px;
            }

            .pane-view-related-topics-panel-pane-1 {
                margin: 3em 0;
                padding: 3em 0;
                border-top: 1px solid #d8d8d8;
                border-bottom: 1px solid #d8d8d8;
            }

            .pane-node-field-author-associations {
                .field-name-field-author-associations {
                    > .field-items {
                        display: flex;
                        flex-wrap: wrap;

                        > .field-item {
                            padding: 1em;
                            margin: 0;
                            width: 50%;
                        }
                    }
                }

                .field-items {
                    margin-left: -1em;
                    margin-right: -1em;

                    .field-item {
                        margin: 1em;
                        text-align: center;

                        .node-person-record {
                            h4 {
                                font-size: 13px;
                            }

                            .panel-panel-inner {
                                font-size: 13px;
                            }

                            .field-items {
                                display: block;

                                .field-item {
                                    margin: 0;
                                    float: none;
                                    min-width: 100%;
                                    max-width: none;

                                    a {
                                        img {
                                            margin-bottom: 1em;
                                            border-radius: 50%;
                                            overflow: hidden;
                                            max-width: 100px;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .related_articles {
                ul {
                    list-style: none;
                    margin: 0;
                }
            }

            .articleCategories,
            .articleTags,
            .pane-node-field-related-content .field-item,
            .related_articles,
            div[class*='pane-view-related-topics-panel'] .field-content {
                a {
                    color: @color__brand-blue;
                    display: inline;
                    transition: color 0.2s;
                    margin-left: 1.1em;
                    position: relative;

                    &:focus,
                    &:hover {
                        border-bottom: 1px solid #e6a839;
                    }

                    &::before {
                        position: absolute;
                        left: -17px;
                        top: 0.125em;
                        content: '';
                        width: 0.875em;
                        height: 0.875em;
                        display: inline-block;
                        background-repeat: no-repeat;
                        background-size: 100%;
                        background-size: contain;
                        background-position: center;
                        margin-right: 5px;
                        background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23e6a839' viewBox='0 0 512 512'%3E%3Cpath d='M416 208H272V64c0-17.67-14.33-32-32-32h-32c-17.67 0-32 14.33-32 32v144H32c-17.67 0-32 14.33-32 32v32c0 17.67 14.33 32 32 32h144v144c0 17.67 14.33 32 32 32h32c17.67 0 32-14.33 32-32V304h144c17.67 0 32-14.33 32-32v-32c0-17.67-14.33-32-32-32z'/%3E%3C/svg%3E");
                    }
                }
            }

            .articleTags {
                display: inline-block;
                color: @color__white;

                a {
                    display: inline-block;
                    float: left;
                    clear: both;
                    margin-bottom: 3px;
                    border-bottom: 1px solid transparent;

                    &::before {
                        position: absolute;
                        left: -17px;
                        right: auto;
                        top: 2px;
                        content: '';
                        width: 0.875em;
                        height: 0.875em;
                        display: block;
                        background: none;
                        background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23e6a839' viewBox='0 0 512 512'%3E%3Cpath d='M416 208H272V64c0-17.67-14.33-32-32-32h-32c-17.67 0-32 14.33-32 32v144H32c-17.67 0-32 14.33-32 32v32c0 17.67 14.33 32 32 32h144v144c0 17.67 14.33 32 32 32h32c17.67 0 32-14.33 32-32V304h144c17.67 0 32-14.33 32-32v-32c0-17.67-14.33-32-32-32z'/%3E%3C/svg%3E");
                        float: left;
                        margin-right: 4px;
                    }

                    &:hover {
                        border-bottom: 1px solid @color__brand-gold;
                    }
                }
            }
        }

        .relatedArticles {
            margin: 3em 0;
            padding: 3em 0;
            border-top: 1px solid #d8d8d8;
            border-bottom: 1px solid #d8d8d8;

            .articleRelated {
                margin: 0;
                margin-left: 1.1em;

                ul {
                    margin: 0 0 1em;

                    li {
                        position: relative;

                        &::before {
                            position: absolute;
                            right: 100%;
                            top: 7px;
                            content: '';
                            width: 0.875em;
                            height: 0.875em;
                            display: inline-block;
                            background-repeat: no-repeat;
                            background-size: 100%;
                            background-size: contain;
                            background-position: center;
                            margin-right: 4px;
                            background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23e6a839' viewBox='0 0 512 512'%3E%3Cpath d='M416 208H272V64c0-17.67-14.33-32-32-32h-32c-17.67 0-32 14.33-32 32v144H32c-17.67 0-32 14.33-32 32v32c0 17.67 14.33 32 32 32h144v144c0 17.67 14.33 32 32 32h32c17.67 0 32-14.33 32-32V304h144c17.67 0 32-14.33 32-32v-32c0-17.67-14.33-32-32-32z'/%3E%3C/svg%3E");
                        }
                    }
                }

                a {
                    display: inline-block;
                    color: @color__brand-blue;

                    &:focus,
                    &:hover {
                        border-bottom: 1px solid #e6a839;
                    }
                }
            }

            h2 {
                font-size: 1.125em;
                text-transform: uppercase;
            }

            h2,
            h2 a {
                color: @color__brand-blue;
            }
        }

        .article-author-container {
            div[id$='ViewReports_Visualizer_pnlContent'] {
                display: flex;
                flex-wrap: wrap;
                margin-left: -1em;
                margin-right: -1em;
                justify-content: center;

                .DNN_Reports_HTML_Item {
                    flex: 1;
                    float: left;
                    min-width: 150px;
                    max-width: 170px;
                    margin: 1em;
                    text-align: center;

                    li {
                        list-style: none;

                        .article-author {
                            font-size: 13px;
                            line-height: 1.4;

                            .article-author--photo {
                                display: inline-block;
                                border-radius: 50%;
                                overflow: hidden;
                                margin-bottom: 10px;

                                img {
                                    width: 120px;
                                    height: 120px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (min-width: @breakpoint__medium) {
    .iif {
        .site--main {
            .article-author-container {
                div[id$='ViewReports_Visualizer_pnlContent'] {
                    justify-content: flex-start;
                }
            }
            .news {
                &.news__latest {
                    .news--advocacy {
                        border: 1px solid lighten(@color__brand-gold, 20%);
                        border-top: 0;
                        border-bottom: 0;
                    }

                    .row--column {
                        flex-basis: 0;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 992px) {
    .iif {
        .site--main {
            .NewsArticles {
                .articleEntry {
                    &.articleEntry__converted {
                        .container,
                        .container-fluid {
                            .content-wrapper {
                                .row {
                                    &.sidebar-layout {
                                        > .panel-panel {
                                            &.col-md-8 {
                                                padding: 0;

                                                .panel-display {
                                                    margin: 0;
                                                }
                                            }

                                            &.col-md-4 {
                                                display: none;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }

                    .container,
                    .container-fluid {
                        .content-wrapper {
                            .row {
                                &.sidebar-layout {
                                    margin-left: -30px;
                                    margin-right: -30px;

                                    .panel-panel {
                                        &.col-md-8 {
                                            flex: 2;
                                            padding: 0 30px;
                                        }

                                        &.col-md-4 {
                                            flex: 1;
                                            padding: 0 30px;
                                            border-left: 1px solid #e6a839;
                                        }
                                    }

                                    .pane-view-related-topics-panel-pane-1 {
                                        margin: 0;
                                        padding: 1.125em 0;
                                        border-top: none;
                                        border-bottom: none;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
