.country-listing {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin: 2em 0 0;

    li {
        flex-basis: 25%;

        a {
            color: #052d6e;
            font-weight: 900;
            display: table;
            margin: 1em;
            padding: 5px 15px 5px 20px;
            transition: color 0.2s;
            position: relative;

            &:focus,
            &:hover {
                color: #477e9e;
            }

            &:focus {
                text-decoration: underline;
            }

            &::before {
                content: '\f067';
                font-family: Font Awesome\ 5 Free;
                color: @color__brand-gold;
                width: 1em;
                height: 1em;
                transform: translateY(0.25em);
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }
}

.news--regional {
    display: flex;
    flex-wrap: wrap;
    margin: 2em -1em 0;
    clear: both;

    .feed--article {
        flex-basis: percentage(1/3);
    }

    .regional--card {
        padding: 1em;
        flex-basis: percentage(1/3);
    }

    .regional--post {
        border: 3px solid #09537d;
        display: block;
        padding: 1em 1.5em 1.5em;
        border-radius: 1em;
        color: #09537d;
        transition: background-color 0.2s, color 0.2s;

        .post--date,
        .post--title {
            display: block;
            line-height: 1.25;
            color: #09537d;
            margin: 0;
            transition: color 0.2s;
        }

        .post--title {
            font-size: 18px;
            margin-bottom: 20px;
            font-weight: normal;
        }

        &:hover {
            background-color: #09537d;
            color: white;
            box-shadow: 7px 7px 9px 0 rgba(189, 190, 191, 0.75);

            .post--date,
            .post--title {
                color: white;
            }
        }
    }

    & + .regional--see-all {
        float: right;
        font-weight: 900;
        text-transform: uppercase;
        color: #09537d;
        transition: color 0.2s;

        &:focus {
            text-decoration: underline;
        }

        &:focus,
        &:hover {
            color: #477e9e;
        }
    }
}
