.content-breakdown {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;

	.content-breakdown--content {
		flex: 1 1 auto;
		margin: 2em 15px;
		min-width: 280px;
		text-align: center;

		.content--number {
			font-size: 24px;
			color: #052d6e;
		}
		.content--text {
			font-size: 26px;
			margin-top: 15px;
			color: #7f7f7f;
		}
		.content--icon {
			margin-top: 15px;

			img {
				max-height: 65px;
			}
		}
	}
}
