.file-download--wrapper {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -10px;

	.file-download {
		flex: 1 0 280px;
		border: 2px solid #09537d;
		border-radius: 15px;
		padding: 10px 15px 10px 10px;
		display: flex;
		max-width: calc(100% / 3);
		min-width: 280px;
		margin: 10px;

		.file-download--link {
			display: flex;
			flex-wrap: nowrap;
			color: #4d4d4d;

			.file-download--icon {
				flex: 1 0 auto;
				border-right: 1px solid #999;
				font-size: 30px;
				display: flex;
				justify-content: center;
				align-items: center;
				min-width: 50px;
				max-width: 50px;
				padding-right: 10px;

				.fa,
				.fas {
					color: @color__brand-gold;
				}
			}
			.file-download--details {
				flex: 2;
				padding-left: 15px;

				.file-download--date,
				.file-download--label,
				.file-download--title {
					display: block;
					width: 100%;
				}

				.file-download--date {
					margin-bottom: 10px;
				}
			}
		}
	}
}
