a,
a.SkinObject {
    text-decoration: none;
    color: @color__link;

    &:hover {
        color: @color__link__hover;
    }
    &:focus {
        color: @color__link__focus;
    }
    &:active {
        color: @color__link__active;
    }

    &:visited {
        color: @color__link__visited;

        &:hover {
            color: @color__link__hover;
        }
        &:focus {
            color: @color__link__focus;
        }
    }
}

a img {
    border: none;
}

a[href^='mailto:'],
a[target='_blank'] {
    // These links tend to be long and could break layout
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-word;
    hyphens: auto;
}
