button,
input[type='submit'],
input[type='button'],
input[type='reset'],
.button {
    .button-styles(primary);
}
.button {
    &.button__secondary {
        .button-styles(secondary);
    }
    &.button__neutral {
        .button-styles(neutral);
    }
    &.button__link {
        .button-styles(link);
    }
    &.button__big {
        font-size: 1.5em;
    }
}

