.anchor {
    position: absolute;
    top: -8em;
}

.btn {
    color: #fff !important;
    padding: 0.75em 1.5em;
    transition: all 0.25s ease;
    background-color: #942f28;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    margin: 0 auto;
    max-width: 250px;
    display: inline-block;
    box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, 0.25);
    border-radius: 6px;

    &.btn-default {
        color: #333 !important;
    }
}

