// Header Global Nav Menu (mobile styles)

.header--nav-toggle {
    position: absolute;
    z-index: 1000;
    background: none;
    padding: 6px;
    background: @color__brand-dark-blue;
    min-width: 50px;
    height: 40px;
    right: 10px;

    &:active,
    &:focus,
    &:hover {
        background: none;
        box-shadow: none;
    }
}

.icon__bars {
    height: 25px;
    width: 25px;
    fill: #fff;
}

.navbar-collapse {
    &[aria-hidden='false'] {
        display: block;
        margin-top: 4em; // margin top for mobile button
    }

    &[aria-hidden='true'] {
        display: none;
    }
}

#MobileNavbar[aria-hidden='true'] nav {
    transform: translate(100%);
}

#MobileNavbar[aria-hidden='false'] nav {
    &[data-current-level='1'] {
        transform: translate(-100%);
    }

    &[data-current-level='2'] {
        transform: translate(-200%);
    }

    &[data-current-level='3'] {
        transform: translate(-300%);
    }
}

.header--navbar.header--navbar__mobile {
    //border-top: 1px solid lighten(@color__brand-blue, 10%);
    padding-top: 1em;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1),
        0 1px 0 rgba(255, 255, 255, 0.1);

    .container {
        padding: 0 !important;
    }

    .row {
        margin: 0;
    }

    .navbar--menus {
        padding: 0; //remove row padding
        width: 100%;
    }

    .header--top-nav {
        ul {
            margin: 0 auto;
        }
    }

    .header--search {
        justify-content: center;
        border-bottom: 1px solid @color__brand-dark-blue;
        padding: 1em 0 2em;

        #dnn_ctl01_dnnSearch_ClassicSearch,
        #dnn_dnnSearch_ClassicSearch {
            width: 100%;
            position: relative;

            .searchInputContainer {
                display: block;
                width: 100%;
                padding: 0 15px;
                position: relative;

                input {
                    width: 100%;
                    font-size: 15px;
                }
            }

            a.SkinObject {
                top: 0;
                right: 15px;
            }
        }
    }

    .row__sign-in {
        display: block;
        padding-bottom: 1em;
        margin-bottom: 1em;
        border-bottom: 1px solid @color__brand-dark-blue;

        .mx-login-mode-wrapper {
            .mx-welcome-back-label {
                display: none;
            }
            .mx-login-mode-ul {
                .mx-li {
                    padding: 0.75em 1em;
                }
            }
            .mx-actions-fieldset {
                .mx-sign-in-span,
                .mx-create-new-user-account-button,
                .mx-signin-help-button {
                    padding: 0.75em 1em;
                }
            }
        }
    }

    .header--main-nav {
        color: @color__white;
        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1),
            0 1px 0 rgba(255, 255, 255, 0.1);
        z-index: 100;
        display: block;
        top: 0;
        left: 0;
        right: 0;
        transition: transform @timing;
        position: relative;
        width: 100%;

        .menu {
            width: 100%;
            margin: 0;
            .clearfix;
            padding: 0;

            li {
                &.menu-item__parent {
                    position: initial;

                    .submenu-toggle {
                        float: right;
                        text-align: right;
                        z-index: 100;
                        text-transform: capitalize;
                        font-weight: normal;
                        font-size: 1em;
                        .icon__svg-background(
                            24px,
                            #fff,
                            @path__chev-right,
                            24
                        );
                        width: 50px;
                        height: 48px;
                        background-position: center center;
                        background-size: 30px;
                        text-indent: 999px;
                        overflow: hidden;
                        clear: right;
                        margin-top: -10px;
                        margin-right: -15px;
                    }
                }

                &.menu-item__parent::after {
                    position: absolute;
                    top: 50%;
                    right: 10px;
                    content: '';
                    width: 0;
                    height: 0;
                    border-top: 5px solid transparent;
                    border-bottom: 5px solid transparent;
                    border-left: 10px solid #bcbdc0;
                    transform: translateY(-50%);
                    border: 0;
                }

                &.menu-item__parent::before {
                    width: 0;
                    height: 0;
                }

                &.menu-item__back {
                    .submenu-toggle {
                        display: none;
                    }
                }

                &::before {
                    width: 0;
                    height: 0;
                }
            }

            &.menu__submenu {
                background-color: @color__brand-blue;
                position: absolute;
                left: 100%;
                top: 0;
                padding: 0;
                transform-origin: left;
                transition: all 0.25s ease;
                z-index: 1;

                &[data-level='0'] {
                    z-index: 2;
                }

                &[data-level='1'] {
                    z-index: 3;
                }

                &[data-level='2'] {
                    z-index: 4;
                }

                &[aria-hidden='true'] {
                    left: 100%;
                    transition: all 0.25s ease;
                }

                &[aria-hidden='false'] {
                    left: 0;
                    transition: all 0.25s ease;
                }
            }
        }

        .menu-item {
            list-style: none;
            width: 100%;
        }

        .menu-link {
            display: block;
            transition: none;

            &:hover {
                background-color: @color__brand-blue-grey;
                color: @color__brand-blue;
            }
        }

        a.menu-link__back {
            .icon__svg-background(24px, #fff, @path__chev-left, 24);
            width: 100%;
            background-position: left center;
            background-size: 30px;

            span {
                margin-left: 1em;
            }
        }

        ul {
        }

        li {
            border-bottom: 1px solid @color__brand-dark-blue;
        }

        a {
            color: @color__white;
            font-size: 1.25em;
            font-weight: 700;
            margin: 0;
            padding: 0.75em;

            &:hover {
                color: @color__white;
            }
        }
    }
}

