// Mid-Size / Average Tablet
@media screen and (min-width: @breakpoint__medium) {
    .navbar-collapse[aria-hidden='true'] {
        display: block;
    }

    .row__sign-in {
        border: none;
    }

    .sign-in--register {
        display: block; // show registration link and instructions on mobile
        font-size: 16px;
    }

    .header--nav-toggle,
    .header--navbar__mobile,
    .menu-item__back {
        display: none;
    }
}

