.gallery-intro {
	padding: 0 0 1em 0;
	text-align: center;
}

.gallery {
	ul.gallery--filter {
		display: inline-block;
		width: 100%;
		margin: 0;
		font-size: @font-size__base;

		li.filter--item {
			list-style: none;
			float: left;
			margin: 0.5em;
			padding: 0.5em;
			border: 2px solid transparent;
			transition: all 0.25s ease;
			font-size: 90%;
			font-weight: normal;
			letter-spacing: 1px;

			&.filter--item__active {
				color: @color__brand-blue;
				border: 2px solid @color__brand-blue;
			}

			&:hover {
				cursor: pointer;
				color: @color__brand-blue;
				border: 2px solid @color__brand-blue;
			}
		}
	}
	.gallery--content {
		display: flex;
		flex-wrap: wrap;

		.gallery--item {
			opacity: 0;
			transform: scale(0);
			transition: all 0.25s ease;
			flex: 0;

			.item--image {
				position: relative;
				z-index: 1;
				img {
					width: auto;
					max-width: 100%;
					height: auto;
					box-shadow: 0 2px 6px -2px #bebebe;
					margin: 0 auto;
				}
			}

			.responsive-media {
				.fancybox-link {
					position: absolute;
				}

				iframe {
					margin: 0 auto;
					pointer-events: none;
					box-shadow: 0 2px 6px -2px #bebebe;
				}
			}

			video {
				max-width: 100%;
				margin: 0 auto;
			}

			.fancybox-link {
				display: block;
				width: 100%;
				height: 100%;
				text-align: center;
			}

			&.gallery--item__active {
				opacity: 1;
				transform: scale(1);
				flex: 1 1 100%;
				padding: 0.5em;
			}

			&.gallery--item__hover {
			}

			.item--caption {
				padding: 0.5em 0;
				border-top: 0;
				position: relative;
				z-index: 2;
				text-align: center;

				.caption--text {
					padding: 0.5em 1em;

					&.caption--text__title {
						display: block;
						color: @color__brand-blue;
						font-weight: bold;
						font-size: 1.1em;
						text-align: center;
					}
					&.caption--text__description {
						display: inline-block;
						margin: 0 auto;
						text-align: left;
					}
				}
			}
		}
	}
}

// Fancybox Overrides

.fancybox-container {
	z-index: 9999999 !important;
}

.fancybox-caption__body strong {
	font-size: 1.3em;
}

// Media Queries
@media screen and (min-width: @breakpoint__small) {
	.gallery {
		.gallery--content {
			.gallery--item {
				&.gallery--item__active {
					flex: 1 1 calc(50% - 1em);
				}
			}
		}
	}
}

@media screen and (min-width: @breakpoint__medium) {
	.gallery {
		.gallery--content {
			&[columns='2'] {
				.gallery--item {
					&.gallery--item__active {
						flex: 1 1 calc(50% - 1em);
					}
				}
			}
			&[columns='3'] {
				.gallery--item {
					&.gallery--item__active {
						flex: 1 1 280px;
						min-width: calc(100% / 3);
					}
				}
			}
			&[columns='4'] {
				.gallery--item {
					&.gallery--item__active {
						flex: 1 1 280px;
						min-width: calc(100% / 4);
					}
				}
			}
		}
	}
}
