.dataContainer {
    [class*='col-'].border {
        background: #fff;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 0.25rem;
        padding: 15px 15px 0 15px;
        margin: 0 0 15px 0;
        position: relative;
        display: inline-block;
        width: 100%;
    }

    p {
        margin: 0;
    }

    h5.text-info {
        margin-top: 5px;

        input {
            border: 0;
            color: #09537d !important;
            font-size: 16px;
            background-image: none !important;
            font-family: Arial, Helvetica, sans-serif;
            width: 100%;
        }
    }

    ul {
        max-height: 250px;
        overflow: hidden;
        overflow-y: scroll;
        position: relative;
        left: -19px;
        right: auto;
        z-index: 1000;
        background: #fff;
        border: 1px solid rgba(0, 0, 0, 0.15);
        padding: 15px;
        list-style: none;
        width: 100%;
        border-bottom-left-radius: 0.25em;
        border-bottom-right-radius: 0.25em;
        box-shadow: 0px 6px 9px -6px #aaa;

        li {
            padding-bottom: 10px;
        }

        &[hidden] {
            max-height: 0;
        }
    }

    .selectCountriesDiv,
    .selectDataDiv,
    .selectYearsDiv {
        height: 25px;
    }

    .countriesContainer {
        .countries {
            background: #09537d;
            padding: 10px 25px 10px 15px;
            float: left;
            margin: 0 10px 15px 0;
            position: relative;
            color: #fff;
            border-radius: 4px;
            font-size: 13px;
            z-index: 1000;

            .remove {
                position: absolute;
                top: 0;
                right: 0;
                height: 25px;
                width: 25px;
                text-align: right;
                padding-right: 6px;
                padding-top: 2px;
                color: #aaa;
                font-size: 10px;
                cursor: pointer;
            }
        }
    }

    a.downloadBtn,
    .downloadBtn {
        background: #09537d;
        padding: 10px 15px;
        position: relative;
        color: #fff;
        border-radius: 4px;
        font-size: 15px;
        display: inline-block;
        text-decoration: none !important;
        transition: opacity 0.15s ease;

        &:hover {
            opacity: 0.9;
        }
    }
}
