.social-media {
	ul {
		margin: 0;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	li {
		list-style: none;
	}
	span {
		text-transform: uppercase;
		font-size: 1.2em;
	}

	a {
		display: block;
		color: @color__white;
		padding: 0.5em 0 0.5em 0.75em;
	}

	.icon__social {
		.svg-icon(2em, @color__white);
	}
}
