.displayGDPR {
    .mx-global-form-wrapper {
        padding: 15px;
        margin: 15px 0;
        border-top: 1px solid #f1d094;
        border-bottom: 1px solid #f1d094;
    }

    .RadPanelBar_Default {
        background: transparent !important;

        .rpGroup {
            background: transparent !important;
            background-color: transparent !important;
        }
    }

    .mx {
        table {
            margin: 0 auto !important;
        }
        .form-row {
            color: #fff !important;
        }
    }
    .mx-panelbar {
        ul.rpRootGroup {
            border: 0 !important;

            li.rpItem.rpFirst {
                a.rpLink {
                    display: none !important;
                }
            }
        }
    }
    .form-group {
        &.mx-left-col.col-md-6.d-none {
            display: none !important;
        }
        &.mx-right-col.col-md-6 {
            width: 100% !important;
            text-align: center !important;
            min-width: 100% !important;
        }
    }

    fieldset {
        text-align: center;
    }
}
