ul {
	&.archive-list {
		list-style: none;
		margin: 0;

		li {
			margin: 5px 0;

			a {
				color: @color__brand-blue !important;
				padding-left: 20px;
				position: relative;
				display: block;

				&:before {
					.icon__svg-background(
						15px,
						@color__brand-gold,
						@path__icon-plus__fontawesome,
						512
					);
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
					background-position: center center;
				}
			}
		}
	}
}
