.mx {
    &.dnnsharp-form {
        .btn {
            color: #fff !important;
            padding: 0.75em 1.5em;
            transition: all 0.25s ease;
            background-color: #942f28 !important;
            font-weight: 700;
            text-transform: uppercase;
            text-align: center;
            margin: 0 auto;
            max-width: 250px;
            display: inline-block;
            box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, 0.25);
            border-radius: 6px;
            border: 0 !important;
        }
        .form-group {
            display: flex;
            flex-wrap: wrap;

            label,
            .label-right {
                min-width: 100%;
            }
        }
        .input-group {
            .input-group-btn {
                .btn {
                    height: 38px;
                    background: #fff !important;
                    border: 1px solid #ccc !important;
                    border-left: 0 !important;
                    min-width: 50px;
                    box-shadow: none;
                    border-radius: 0 4px 4px 0;
                    color: #333 !important;
                    padding: 0.75em 1.25em;

                    .glyphicon {
                        &.glyphicon-chevron-down {
                            .icon__svg-background(
                                13px,
                                #333,
                                @path__chevron-down,
                                512
                            );
                        }
                        &.glyphicon-chevron-up {
                            .icon__svg-background(
                                13px,
                                #333,
                                @path__chevron-up,
                                512
                            );
                        }
                    }
                }
            }
        }
        .checkbox-list {
            .dropdown-absolute {
                width: 100% !important;

                .dropdown-panel {
                    width: 100% !important;
                    border: 1px solid #ccc;
                    padding: 15px;
                    border-top: 0;
                }
            }
        }
    }
}
