@font-face {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: normal;
    src: url('/Portals/_default/Skins/IIF/fonts/Avenir/AvenirLTCom-Medium.woff') format('woff');
}
@font-face {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 600;
    src: url('/Portals/_default/Skins/IIF/fonts/Avenir/AvenirLTCom-Medium.woff') format('woff');
}
@font-face {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 400;
    src: url('/Portals/_default/Skins/IIF/fonts/Avenir/AvenirLTCom-Book.woff') format('woff');
}
@font-face {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 700;
    src: url('/Portals/_default/Skins/IIF/fonts/Avenir/AvenirLTCom-Black.woff') format('woff');
}
@font-face {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 900;
    src: url('/Portals/_default/Skins/IIF/fonts/Avenir/AvenirLTCom-Black.woff') format('woff');
}

body,
html {
    font-family: @font__sans-serif !important;
    font-size: @font-size__base;
}

p {
    font-size: 1em;
    line-height: 1.314;

    &.lead {
        font-size: 1em;
        line-height: 1.314;
    }
}

.Normal,
.NormalDeleted,
.NormalDisabled {
    font-size: 1em;
    line-height: 1.314;
}

/// Headings ///
h1,
h2,
h3,
h4,
h5,
h6 {
    color: @color__brand-blue;
    font-family: @font__sans-serif !important;
}

h1 {}

h2 {}

h3 {}

h4 {}

h5 {}

h6 {}

blockquote {
    p {}
}

small {}

