.article-listing--pager {
    display: table;
    margin: 0 auto;

    &.pager__table {
        .pager--page-list {
            display: inline;
            margin: 0;

            &::after,
            &::before {
                display: none;
            }
        }
    }

    .pager-link-styles() {
        display: inline-block;
        position: relative;
        padding: 5px 14px;
        margin: 0 0.5em;
        color: #555;
        font-size: 12px;
        font-weight: bold;
        line-height: 1.6;
        border-radius: 15px;
        border: 1px solid #ddd;
        background-color: #fff;
        text-transform: uppercase;
    }

    .pager--link {
        .pager-link-styles;

        &:focus,
        &:hover {
            background-color: #eee;
            color: #477e9e;
        }

        &::after,
        &::before {
            vertical-align: text-top;
            line-height: 1;
        }

        &.link__first {
            &::before {
                content: '«';
                margin-right: 0.5em;
            }
        }

        &.link__prev,
        &.link__previous {
            &::before {
                content: '‹';
                margin-right: 0.5em;
            }
        }

        &.link__next {
            &::after {
                content: '›';
                margin-left: 0.5em;
            }
        }

        &.link__last {
            &::after {
                content: '»';
                margin-left: 0.5em;
            }
        }
    }

    .pager--page-list {
        display: inline;
        margin: 0;

        &::after,
        &::before {
            display: inline;
            content: "...";
            vertical-align: bottom;
            font-size: 1.25em;
            margin: 0.5em;
        }
    }

    &.pager__end .pager--page-list::after,
    &.pager__start .pager--page-list::before {
        display: none;
    }

    &.pager__end {
        .pager--link {
            &.link__last,
            &.link__next {
                display: none;
            }
        }

    }

    &.pager__start {
        .pager--link {
            &.link__first,
            &.link__prev,
            &.link__previous {
                display: none;
            }
        }
    }

    .pager--page {
        list-style: none;
        display: none;

        &.page__link {
            &.page__next,
            &.page__prev {
                display: inline;
            }
        }

        &.page__current,
        &.page__link a {
            .pager-link-styles;
        }

        &.page__current {
            display: inline-block;
            border-color: #09537d;
            background-color: #09537d;
            color: white !important;
        }

        &.page__link a {
            &:focus,
            &:hover {
                background-color: #eee;
                color: #477e9e;
            }
        }
    }
}

.PagingTable {
    display: none;
}
