.site--main {
    .rotator {
        margin-bottom: 40px;

        .banner--image {
            width: 100%;
        }

        .banner--content {
            display: none;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            flex-direction: column;
            padding-bottom: 1em;
            justify-content: space-around;
            font-size: 0.75em;
        }

        .swiper-slide-active {
            .banner--content {
                display: flex;
            }
        }

        .banner--overlay {
            background-color: fade(@color__white, 90%);
            padding: 0.5em .75em;
            font-size: 1.333em;

            > * {
                color: @color__brand-dark-blue !important;
                font-weight: bold;
            }

            .banner--title {
                font-size: 1.25em;
                margin-top: 0;
                margin-bottom: 0.5em;
                line-height: 1.25;
            }

            .banner--text {
                margin: 0;
                line-height: 1.5;
                font-weight: normal;
            }
        }

        .banner--link {
            background-color: @color__brand-gold;
            font-weight: 900;
            color: white;
            font-size: 1.125em;
            border-radius: 0.5em;
            box-shadow: 0.333em 0.333em 0.333em 0 fade(@color__black, 20%);
            align-self: center;
            padding: 0.666em 1em;
            text-transform: uppercase;
            letter-spacing: 1px;
        }

        .swiper-pagination {
            width: auto !important;
            bottom: auto;
            top: 0.5em;
            left: 0 !important;
            right: auto !important;

            .swiper-pagination-bullet {
                width: 1.5em;
                height: 1.5em;
                margin: 0 0.333em;
                background: #fff;
                opacity: 1 !important;
                background-color: hsla(0, 0%, 50%, 0.8);
                text-indent: 999px;
                overflow: hidden;
                outline: none;
                position: relative;

                &::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    background-color: white;
                    transform: scale(0.125);
                    transition: transform 0.25s ease-in-out;
                    border-radius: 50%;
                    will-change: transform;
                }

                &:focus {
                    outline: none;
                }

                &.swiper-pagination-bullet-active {
                    &::before {
                        transform: scale(0.75);
                    }
                }
            }
        }
    }

    .cta-banner {
        background-size: cover;
        box-shadow: 7px 7px 9px 0 rgba(189, 190, 191, 0.75);
        position: relative;
        overflow: hidden;

        &.cta-banner__filtered {
            &:before {
                content: '';
                position: absolute;
                background: #fff;
                bottom: 0;
                top: -50%;
                right: 0;
                left: 0%;
                height: 300%;
                width: 200%;
                transform: rotate3d(1, 0, -0.16, -0.8275turn);
                z-index: 1;
            }
        }

        .banner--content {
            padding: 1em 3em;
            display: flex;
            flex-wrap: wrap;
            position: relative;
            z-index: 2;

            .banner--icon {
                min-width: 100%;
                margin-bottom: 1em;
                margin-right: 3em;

                img {
                    width: auto;
                    height: auto;
                    max-width: 100%;
                }
            }

            .banner--text {
                flex: 1;
                font-weight: bold;
                color: @color__brand-dark-blue;
                line-height: @line-height__base;

                .banner--cta {
                    a {
                        color: #fff;
                        padding: 0.75em 1.5em;
                        transition: all 0.25s ease;
                        background-color: #c71f27;
                        font-weight: 700;
                        text-transform: uppercase;
                        text-align: center;
                        margin: 1em auto 0 auto;
                        max-width: 250px;
                        display: block;
                        box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, 0.25);
                        border-radius: 6px;
                    }
                }
            }
        }
    }
}
@media screen and (min-width: @breakpoint__medium) {
    .site--main {
        .cta-banner {
            max-width: 90%;
            margin: 0 auto;

            &.cta-banner__filtered {
                &:before {
                    top: -75%;
                    right: 0;
                    left: -10%;
                    height: 300%;
                    width: 200%;
                    transform: rotate3d(1, 0, -0.15, -0.8275turn);
                }
            }

            .banner--content {
                .banner--icon {
                    min-width: 0;
                    margin-right: 3em;
                }
            }
        }
    }
}
@media screen and (min-width: @breakpoint__large) {
    .site--main {
        .rotator {
            .banner--content {
                position: absolute;
                display: flex;
                padding: 2em 2em 4em;
            }

            .swiper-pagination {
                bottom: 1.25em;
                top: auto;
                right: 0 !important;

                .swiper-pagination-bullet {
                    margin: 0 0.666em;
                }
            }
        }
        .cta-banner {
            max-width: 80%;
            margin: 0 auto;

            &.cta-banner__filtered {
                &:before {
                    top: -75%;
                    right: 0;
                    left: -6%;
                    height: 300%;
                    width: 200%;
                    transform: rotate3d(1, 0, -0.15, -0.8275turn);
                }
            }
        }
    }
}
@media screen and (min-width: @breakpoint__1x-large) {
    .site--main {
        .rotator {
            .banner--content {
                font-size: 0.875em;
            }

            .banner--link {
                padding: 1.25em 1.5em;
            }
        }
        .cta-banner {
            max-width: 70%;
            margin: 0 auto;

            &.cta-banner__filtered {
                &:before {
                    top: -75%;
                    right: 0;
                    left: -10%;
                    height: 300%;
                    width: 200%;
                    transform: rotate3d(1, 0, -0.085, -0.8275turn);
                }
            }
        }
    }
}
@media screen and (min-width: @breakpoint__2x-large) {
    .site--main {
        .rotator {
            .banner--content {
                font-size: 1em;
            }
        }
    }
}


// Alternate Banner
/*--- OpenContent Alt Banner Overrides ---*/
.rotator.rotator__alt .swiper-container {
	overflow: visible; /* allow for pagers to be visible outside of container */
	margin-bottom: 3em;
}
.rotator.rotator__alt .banner--content {
	justify-content: flex-end; /* move the content overlay to the bottom */
	padding: 0 !important; /* remove padding around inner banner */
}

.rotator.rotator__alt .swiper-pagination {
	bottom: -2.5em;	/* move pagers outside of container */
    top: auto;
}