.featured-event {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;

	.featured-event--container {
		flex: 1;
		margin: 2em 15px 0 15px;
		padding: 2em 1em;
		position: relative;
		background-size: cover;
		display: flex;
		flex-wrap: wrap;

		.featured-event--content {
			background: rgba(255, 255, 255, 0.9);
			padding: 15px 30px 30px 30px;
			color: @color__brand-deep-blue;
			line-height: 1.314;
			flex: 1;
			min-width: 100%;

			div {
				min-width: 100%;
				margin-top: 5px;
			}

			h1 {
				font-size: 28px;
				margin-bottom: 5px;
				line-height: 1.314;
			}
		}

		.featured-event--cta {
			margin-top: 30px;
			flex: 1;
			display: flex;
			justify-content: center;

			a {
				background: @color__brand-gold;
				color: @color__white;
				padding: 0.75em 2.5em;
				border-radius: 9px;
				font-size: 18px;
				font-weight: bold;
			}
		}
	}
}

@media screen and (min-width: @breakpoint__medium) {
	.featured-event {
		.featured-event--container {
			padding: 3em 2em;

			.featured-event--cta {
				margin-top: 40px;
			}
		}
	}
}
