.panel-container {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;

    .panel-container--icon-panel {
        flex: 1 1 350px;
        margin: 0 -1px;

        .icon-panel--icon {
            text-align: center;
            padding: 10px 0;
            margin: 0 0 30px 0;
            border-left: 1px solid @color__brand-gold;

            img {
                width: auto;
                max-width: 126px;
                height: auto;
                border: 6px solid transparent;
                border-radius: 50%;
            }
        }

        .icon-panel--text {
            padding: 0 45px;
            text-align: center;

            h3 {
                font-size: 22px;
                color: @color__brand-blue;

                a {
                    color: @color__brand-blue;
                    text-decoration: none;
                }

                &:hover {
                    color: lighten(@color__brand-blue, 10%);

                    a {
                        color: lighten(@color__brand-blue, 10%);
                        text-decoration: underline;
                    }
                }
            }
        }

        &:hover {
            .icon-panel--icon {
                img {
                    border-color: @color__brand-gold;
                }
            }
        }

        &:first-child {
            .icon-panel--icon {
                border-left: 0;
            }
        }
    }
}
