.site--main {
    .mx-module-heading {
        display: none;
        margin: 0;
        min-width: 100%;
        padding-bottom: 1em;
        color: @color__brand-blue;
        font-weight: bold;

        &:first-child {
            display: block;
        }
    }

    .event-button {
        .mx {
            &.mx-wrapper {
                input,
                a {
                    &.btn {
                        &.btn-primary {
                            color: @color__white !important;
                            background-color: @color__brand-red !important;
                            border-color: @color__brand-red !important;
                            font-weight: bold !important;

                            &:focus {
                                box-shadow: none !important;
                            }
                        }
                        &.btn-outline-primary {
                            background-color: @color__brand-blue !important;
                            border-color: @color__brand-blue !important;
                            font-weight: bold !important;
                        }
                    }
                    &.mx-edit-hyperlink {
                        &.btn {
                            &.btn-primary {
                                background-color: @color__brand-blue !important;
                                border-color: @color__brand-blue !important;
                                font-weight: bold !important;
                            }
                        }
                    }
                }

                &.mx-meeting-registrationinformation-wrapper {
                    fieldset {
                        input {
                            &.mx-registration-button,
                            &.mx-registerme-button {
                                display: block;
                                margin: 0 auto;
                            }
                        }
                    }
                }
            }
        }
    }

    .event-banner {
        width: 100%;

        img {
            width: 100%;
            height: auto;
        }
    }

    .event-banner-content {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        .mx-meeting-details-wrapper {
            border: 1px solid #ccc;
            padding: 15px;
            box-shadow: 0 0 2px 0 #ccc;

            .mx-meeting-name-li {
                font-family: @font__sans-serif;
                font-size: 28px;
                line-height: 36px;
                letter-spacing: normal;
                margin-top: 0;
                margin-bottom: 5px;
                color: #09537d;
                font-weight: normal;
                display: block;
            }

            .mx-meeting-location-li {
                display: block;
                margin-top: 15px;
            }

            .mx-meeting-info-li {
                display: block;
                margin-top: 5px;
            }

            .mx-buttons-fieldset {
                display: block;
                margin-top: 15px;
            }
        }

        .event-banner-content--info {
            font-size: 1.5rem;
            color: @color__brand-blue;
            display: flex;
            align-items: center;
            margin: 10px 0;
            padding: 0;
            line-height: 1.314;
            text-align: center;

            h1 {
                font-size: 2rem;
                margin: 0;
                color: @color__brand-blue;
                line-height: 1.314;
                width: 100%;
                margin: 0 15px;
            }

            &:first-child {
                border-left: 0;
                margin-left: 0;
                padding-left: 0;
                min-width: 100%;
            }

            &.event-banner-content--info__date {
                padding-right: 15px;
            }

            &.event-banner-content--info__location {
                margin-left: 0;

                span {
                    &.divider {
                        width: 2px;
                        height: 43px;
                        background: #09537d;
                        margin-right: 10px;
                    }
                }
            }
        }
    }

    .event-description {
        .mx-items-panel {
            margin: 0;
        }

        .event-description {
            a {
                min-width: 13em !important;
                max-width: 100% !important;
            }
        }
    }

    .event-actions {
        padding: 2em 0 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        .event-actions--buttons {
            text-align: center;
            min-width: 100%;
            margin-bottom: 3em;

            .btn {
                margin: 1em 3em;
            }
        }

        .event-actions--description {
            flex: 2;
            font-size: @font-size__base;
            line-height: @line-height__base;
            color: @color__brand-blue;
            min-width: 100%;
            margin: 1em 0;

            strong {
                font-size: 18px;
            }
        }

        .event-actions--side-nav {
            flex: 0 1 300px;
            background: #f6f6f7;
            padding: 15px;
            font-size: 18px;
            color: #09537d;
            min-width: 100%;
            margin: 1em 0;

            ul {
                list-style: none;

                li {
                    a {
                        display: block;
                        color: @color__brand-blue;
                        margin: 0 0 5px;
                        position: relative;
                        font-size: @font-size__base;

                        &:before {
                            content: '\002b';
                            color: @color__brand-gold;
                            font-size: 20px;
                            margin-right: 5px;
                            position: absolute;
                            top: 50%;
                            left: -20px;
                            transform: translateY(-50%);
                        }

                        &:hover {
                            text-decoration: underline;
                        }
                    }
                }
            }
        }
    }

    .event-fees {
        .mx {
            &.mx-wrapper {
                padding-bottom: 2rem;
                .mx-modeview-panel {
                    position: relative;

                    .mx-instructions-wrapper {
                        position: absolute;
                        top: 100%;
                    }
                }
            }
        }

        .mx-items-panel,
        .mx-radlistview {
            display: flex;
            flex-wrap: wrap;
            margin-left: -5px;
            margin-right: -5px;
        }

        .event-fees--fee-method {
            background: #efefef;
            flex: 1 0 150px;
            margin: 5px;
            color: #09537d;

            .fee-method--header {
                background: #c9cacc;
                text-align: center;
                padding: 5px;
                text-transform: uppercase;
                font-size: 20px;
            }

            .fee-method--body {
                text-align: center;
                padding: 2.5em 0;
                font-size: 24px;
            }
        }
    }

    .sponsors {
        text-align: center;
        margin: 2em 0;

        .mx-listsortingarea-panel,
        .mx-sort-area-sub-wrapper {
            display: none;
        }

        .mx-title-h3 {
            font-family: @font__sans-serif;
            font-size: 28px;
            line-height: 36px;
            letter-spacing: normal;
            margin-top: 8px;
            margin-bottom: 10px;
            color: #09537d;
            font-weight: normal;
            display: block;
            text-align: center;
        }

        .mx-title-wrapper {
            h3 {
                font-family: @font__sans-serif;
                font-size: 28px;
                line-height: 36px;
                letter-spacing: normal;
                margin-top: 8px;
                margin-bottom: 10px;
                color: #09537d;
                font-weight: normal;
                display: block;
                text-align: center;
            }
        }

        .mx-module-title {
            display: none;

            h3 {
                font-family: @font__sans-serif;
                font-size: 28px;
                line-height: 36px;
                letter-spacing: normal;
                margin-top: 8px;
                margin-bottom: 10px;
                color: #09537d;
                font-weight: normal;
                display: block;
                text-align: center;
            }

            &:nth-child(2) {
                display: block;
                min-width: 100%;
            }
        }

        .mx-items-panel,
        .mx-radlistview {
            display: flex;
            flex-wrap: wrap;
            margin: 0 auto;
            justify-content: center;

            .sponsor {
                flex: 0 1 280px;

                .sponsor--logo {
                    display: inline-block;
                    width: 100%;
                    padding: 15px;
                    text-align: center;

                    .logo {
                        max-width: 100%;
                    }
                }
            }
        }
    }

    .speaker-list {
        .mx-listsortingarea-panel,
        .mx-sort-area-sub-wrapper {
            display: none;
        }

        .mx-items-panel,
        .mx-radlistview {
            display: flex;
            flex-wrap: wrap;
            margin-left: -30px;
            margin-right: -30px;
            justify-content: center;

            .mx-module-heading {
                min-width: 100%;
                margin: 0 30px 0 0;
                overflow: hidden;
            }

            .module-container__border-advanced {
                &:after {
                    left: 60px;
                }
            }

            .speaker-list--speaker {
                flex: 0 1 12.25%;
                margin: 2em 30px;
                text-align: center;
                font-size: 13px;
                min-width: 150px;

                .speaker--image {
                    border-radius: 50%;
                    overflow: hidden;
                    max-width: 100px;
                    margin: 0 auto 10px;

                    img {
                        max-width: 100px;
                        height: auto;
                    }
                }
            }
        }
    }

    .session-list {
        margin: 0 -0.5em;

        .break {
            min-width: 100%;
        }

        .session-list--days {
            margin-bottom: 1em;

            a {
                color: #09537d;
                text-decoration: underline;
                font-size: 24px;
                font-weight: bold;
                border-left: 2px solid #09537d;
                margin-right: 10px;
                padding-left: 10px;

                &:first-child {
                    border: 0;
                    padding-left: 0;
                }
            }
        }

        .session-days {
            display: none;
            width: 100%;
            margin-top: 15px;
            border-bottom: 1px solid #c9cacc;

            .session-days--tab {
                pointer-events: none !important;
                color: @color__white;
                background: @color__brand-blue;
                padding: 0.75em 2.5em 0.75em 2em;
                font-weight: bold;
                display: inline-block;
                position: relative;
                margin-right: 15px;

                &::after {
                    content: '\f067';
                    font-family: Font awesome\5 Free;
                    position: absolute;
                    right: 1em;
                }
            }

            .session-days--instructions {
                display: inline-block;
                font-style: italic;
                color: #999;
                pointer-events: none !important;

                .instructions--open {
                    display: block;
                }
                .instructions--close {
                    display: none;
                }
            }

            &.show {
                display: block !important;
            }

            &.open {
                .session-days--tab {
                    &::after {
                        content: '\f068' !important;
                    }
                }
                .session-days--instructions {
                    .instructions--open {
                        display: none;
                    }
                    .instructions--close {
                        display: block;
                    }
                }
            }

            &:hover {
                cursor: pointer;
            }
        }

        .mx-items-panel,
        .mx-radlistview {
            display: flex;
            flex-wrap: wrap;
            margin: 0 0.5em;

            .session-list--time {
                display: block;
                background: #c9cacc;
                padding: 10px;
                color: #09537d;
                font-size: 20px;
                font-weight: bold;
                border-bottom: 5px solid #fff;
            }

            .session-container {
                width: 100%;
                background: #efefef;
                margin-bottom: 1em;
                display: none;

                &.concurrent-session {
                    flex: 1 0 250px;
                }

                &.show {
                    display: block;
                }

                .session-container--inner {
                    height: 100%;
                }
            }

            .session-list--session {
                display: flex;
                flex-wrap: wrap;

                .session--content {
                    display: flex;
                    flex-wrap: wrap;
                    flex: 1 0 200px;

                    .session--title {
                        flex: 1;
                        min-width: 100%;
                        background: #c9cacc;
                        padding: 5px 10px;
                        color: #09537d;
                        font-size: 22px;
                        font-weight: bold;
                        line-height: 1.314;
                    }

                    .session--description {
                        background: #efefef;
                        padding: 10px;
                        color: #09537d;
                        flex: 1;

                        strong {
                            font-size: 18px;
                        }
                    }

                    &:nth-child(2n) {
                        .session--description {
                            background: #f6f6f7;
                        }
                    }
                }

                &.session-list--session__split {
                    margin-left: -5px;
                    margin-right: -5px;

                    .session--content {
                        margin: 0 5px;
                    }
                }
            }
        }
    }

    .event-videos {
        .mx-items-panel,
        .mx-radlistview {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0 -15px;

            iframe {
                float: left;
                margin: 0 15px 15px;
            }
        }
    }

    .venue {
        display: flex;
        flex-wrap: wrap;
        margin-left: -15px;
        margin-right: -15px;

        .venue--image {
            flex: 1 1 280px;
            padding: 0 15px;
            margin: 1em 0;

            img {
                max-width: 100%;
                height: auto;
            }
        }

        .venue--map {
            flex: 1 1 280px;
            padding: 0 15px;
            margin: 1em 0;
        }
    }

    .mx-meeting-location-wrapper {
        .mx-location-name-link {
            font-family: @font__sans-serif;
            font-size: 28px;
            line-height: 36px;
            letter-spacing: normal;
            margin-top: 8px;
            margin-bottom: 10px;
            color: #09537d;
            font-weight: normal;
            display: block;
        }

        .mx-li.mx-address-li {
            margin-bottom: 5px;
        }
    }

    .registration-grid {
        .mx-radgrid,
        .rgMasterTable {
            display: none;
        }

        .mx-buttons-fieldset {
            .mx-row {
                text-align: center;
            }

            input[type='submit'] {
                color: #fff !important;
                padding: 0.75em 1.5em;
                -webkit-transition: all 0.25s ease;
                transition: all 0.25s ease;
                background-color: #942f28;
                font-weight: 700;
                text-transform: uppercase;
                text-align: center;
                margin: 0 auto;
                max-width: 250px;
                display: inline-block;
                box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, 0.25);
                border-radius: 6px;
            }
        }
    }

    .event-calendar {
        .mx-listsortingarea-panel,
        .mx-sort-area-sub-wrapper {
            display: none;
        }

        .mx-items-panel,
        .mx-radlistview {
            display: flex;
            flex-wrap: wrap;
            margin: 0 -15px;

            .event-calendar--event {
                flex: 1 1 100%;
                margin: 0 15px 15px;
                display: flex;
                flex-wrap: wrap;
                background: #f0f0f0;

                .event--image {
                    border-right: 2px solid #fff;
                    max-width: 100px;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }

                .event--info {
                    flex: 1;
                    padding: 15px;
                    color: #052d6e;

                    a,
                    h4,
                    h5,
                    p {
                        color: #052d6e;
                    }

                    h4 {
                        font-size: 18px;
                        line-height: 1.314;
                    }

                    .info--dates {
                        font-size: 14px;
                        float: left;
                        padding: 0 5px 0 0;
                        margin: 5px 0 0;
                    }

                    .info--location {
                        font-size: 14px;
                        float: left;
                        padding: 0 0 0 5px;
                        margin: 5px 0 0;

                        span {
                            &.divider {
                                width: 1px;
                                height: 16px;
                                background: #444;
                                display: inline;
                                float: left;
                                margin-right: 10px;
                            }
                        }
                    }
                }
            }
        }
    }

    .attendee-list {
        .mx-title-wrapper {
            display: none;
        }

        .mx-items-panel {
            display: flex;
            flex-wrap: wrap;

            .attendee {
                flex: 0 1 25%;
                min-width: 100%;
                position: relative;
                padding-left: 23px;
                margin-bottom: 10px;

                .fa,
                .fas {
                    color: @color__brand-gold;
                    position: absolute;
                    left: 0;
                    font-size: 15px;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }
    }

    .event-buttons {
        fieldset {
            text-align: center;

            input {
                background-color: @color__brand-gold;
                font-weight: 900;
                color: @color__white;
                border-radius: 0.5em;
                box-shadow: 0.333em 0.333em 0.333em 0 rgba(0, 0, 0, 0.2);
                align-self: center;
                padding: 0.666em 1em;
                text-transform: uppercase;
                letter-spacing: 1px;
                border: 0;
                min-width: 13em !important;
                max-width: 100% !important;
            }

            .btn {
                min-width: 13em !important;
                max-width: 100% !important;
            }
        }
    }
    .event-button {
        fieldset {
            input {
                min-width: 13em !important;
                max-width: 100% !important;
            }

            .btn {
                min-width: 13em !important;
                max-width: 100% !important;
            }
        }
    }
}
@media screen and (min-width: @breakpoint__medium) {
    .site--main {
        .event-actions {
            .event-actions--description {
                min-width: 0;
                padding: 0 5em 0 0;
            }

            .event-actions--side-nav {
                min-width: 0;
            }
        }

        .event-calendar {
            .mx-radlistview {
                .event-calendar--event {
                    min-width: 46%;
                    max-width: calc(50% - 30px);
                }
            }
        }

        .attendee-list {
            .mx-modeview-panel {
                .attendee {
                    min-width: 280px;
                }
            }
        }

        .event-banner-content {
            .event-banner-content--info {
                font-size: 2rem;
            }
        }

        .session-list {
            .mx-items-panel,
            .mx-radlistview {
                .session-list--session {
                    .session--content {
                        .session--title {
                            padding: 5px 50px;
                        }
                        .session--description {
                            padding: 10px 10px 10px 50px;
                        }
                    }
                }
            }
        }
    }
}
