* {
    box-sizing: border-box;
}

body,
html {
    overflow-x: hidden;
}

body {
    min-width: @min-grid-width;
}

.iif {
    background: #fff;
}

.container(@max-width:@site-width) {
    position: relative;
    width: 100%;
    max-width: @max-width;
    margin: 0 auto;
    padding-left: (@grid-gutter-width / 2);
    padding-right: (@grid-gutter-width / 2);
    .clearfix;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -(@grid-gutter-width / 2);
    margin-right: -(@grid-gutter-width / 2);
    clear: both;
    width: 100%;

    .row--column {
        padding-left: (@grid-gutter-width / 2);
        padding-right: (@grid-gutter-width / 2);
    }
}

div[class^='col-'] {
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

@media screen and (min-width: 992px) {
    .col-md-8 {
        width: 66.66666667%;
    }
    .col-md-4 {
        width: 33.33333333%;
    }
}

///////// Media Queries /////////

// 1x Small / Small Tablet, Large Phone
@media screen and (min-width: @breakpoint__1x-small) {
}

// Small / Small Tablet
@media screen and (min-width: @breakpoint__small) {
}

// Mid-Size / Average Tablet
@media screen and (min-width: @breakpoint__medium) {
    .iif {
        background: #fff url('./images/globe-bg.png') 100% 0 no-repeat;

        &.iif__home {
            background-image: none;
        }
    }

    .row {
        &.row__sidebar-right {
            .row--column {
                flex: 1;
                padding: 0 30px 0 15px;

                &.row--column__sidebar {
                    flex: 0.485;
                    border-left: 1px solid #dedede;
                    padding: 0 15px 0 30px;
                }

                &.row--column__sidebar__no-border {
                    flex: 0.485;
                    padding: 0 15px 0 30px;
                }
            }
        }

        &.row__sidebar-left {
            .row--column {
                flex: 1;
                padding: 0 15px 0 30px;

                &.row--column__sidebar {
                    flex: 0.485;
                    border: 0;
                    padding: 0 30px 0 15px;
                }
            }
        }
    }
}

