/// Universal Container Styles ///

.module-container {
    margin-bottom: 3em;

    .module-container--heading {}

    .module-container--body {}

    &.module-container__small {
        margin-bottom: 1.5em;
    }
}

/// Title Containers ///
.module-container__h1-title {}

.module-container__h2-title {}

.module-container__h3-title {
    .module-container--heading {
        font-weight: 900;
        font-size: 22px;
        line-height: 1.25;
    }
}

/// Callout Containers ///
.module-container__callout {
    background-color: #eee;
    padding: 2em;
}

.module-container__border-top {
    padding-top: 3.5em;
    border-top: 1px solid @color__brand-gold;

    &.module-container__small {
        padding-top: 1.75em;
    }

    .module-container--heading {
        text-align: center;
    }
}

.module-container__border-top-and-bottom {
    padding-top: 30px;
    padding-bottom: 30px;
    border-top: 1px solid @color__brand-gold;
    border-bottom: 1px solid @color__brand-gold;

    .module-container--heading {
        text-align: center;
    }

    &.module-container__border-blue {
        border-top: 5px solid @color__brand-blue;
        border-bottom: 5px solid @color__brand-blue;
    }
}

.module-container__border-advanced {
    text-align: center;
    margin-bottom: 5em;
    position: relative;

    .module-container--heading {
        text-align: center;
        background: #fff;
        position: relative;
        z-index: 2;
        display: inline-block;
        margin: 0 auto;
        padding: 0 3% 1.5em;
    }

    &::after {
        content: '';
        z-index: 1;
        border-top: 1px solid @color__brand-gold;
        width: 100%;
        position: absolute;
        left: 0;
        top: 20px;
    }

    .module-container--body {
        text-align: left;
    }

    &.module-container__small {
        margin-bottom: 2.5em;
    }
}

.module-container__gold-tab {
    margin-bottom: 0;

    .module-container--heading {
        background: @color__brand-gold;
        padding: 20px;
        margin: 0;
        border-radius: 20px 20px 0 0;
        text-align: center;
        color: @color__white;
        text-transform: uppercase;
        font-weight: bold;

        span {
            margin: 0;
        }
    }
}
/// TSVCM Containers ///
.module-container__h2-tsvcm {
    h2 {
        color: #4a76ce; 
        font-family: Avenir, Lato, Helvetica Neue, Helvetica, Arial, sans-serif !important;
    }
}

.module-container__gray-tab {
    margin-bottom: 0;

    .module-container--heading {
        background: #595959;
        padding: 10px;
        margin: 0;
        text-align: center;
        color: @color__white;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 16px;
        line-height: 1.314;

        span {
            margin: 0;
        }
    }
}

.module-container__plain {
    margin: 0;
}


@media screen and (min-width: @breakpoint__medium) {
    .module-container {
        margin-bottom: 5em;

        &.module-container__small {
            margin-bottom: 2.5em;
        }
    }

    .module-container__plain {
        margin: 0;
    }

    .module-container__gold-tab {
        margin-bottom: 0;
    }

    .module-container__gray-tab {
        margin-bottom: 0;
    }
}
