// General and Shared Nav Styles (Header Top, Header Main, Footer)

nav {
    ul {
        margin: 0;
    }

    li {
        list-style: none;
    }

    a {
        display: block;
        text-transform: uppercase;
        padding: 0.75em 1em;
    }
}

// Header Top Nav Menu
.header--top-nav {
    color: @color__brand-gold;
    margin-bottom: 0.5em;

    ul {
        text-align: center;

        li {
            display: inline-block;
            float: none;
            margin-right: 0;

            &::before {
                background: #f5dcb0;
                width: 2px;
                content: '';
                display: block;
                position: absolute;
                top: 0.25em;
                bottom: 0;
                left: 0;
                right: 100%;
                margin-left: -0.125em;
                height: 0.5em;
            }

            a {
                color: @color__brand-gold;
                padding: 0.35em 0.5em;
                line-height: 1;
                font-size: 12px;

                &:hover {
                    text-decoration: underline;
                }
            }

            &:last-child {
                a {
                    padding-right: 0;
                }
            }
        }
    }
}

.header--main-nav {
    ul {
        li {
            position: relative;
            margin-right: 0;

            a {
                padding: 0.75em 0;
                line-height: 1;
                margin: 0 0.75em;
                font-size: 1.2em;
                line-height: 1.314;

                &[data-level='0'] span {
                    font-weight: 900;
                    font-size: 1em;
                }
            }

            &:last-child {
                a {
                    margin-right: 0;
                }
            }
        }
    }
}

// Interior Nav (hortizontal menu above main section of Detail Layout)
.interior-nav {
    background-color: rgba(0, 0, 0, 0.225);
    margin-bottom: 3em;

    ul {
        margin: 0;
        overflow-x: hidden;
        padding-bottom: 1px;
        width: 100%;
        overflow: visible;

        li {
            float: none;
            position: relative;
            margin: 0 5px 0 0;
            list-style: none;

            &::before {
                content: none;
            }

            &:last-child {
                margin-right: 0;
            }

            &:not(:first-child) {
                border-top: 1px solid #477e9e;
            }
        }

        a {
            color: @color__brand-blue;
            font-weight: 800;
            padding: 1.25em 1em;
            line-height: 1.7;
            font-size: 13px;

            &:hover {
                color: @color__brand-blue;
            }
        }

        &::after,
        &::before {
            content: '';
            display: table;
            clear: both;
        }
    }

    .menu__submenu {
        display: none;
        background-color: @color__white;
        border: 1px solid #ccc;
        border-top: none;
        min-width: 20em;
        position: absolute;
        top: 102%;
        left: 0;
        z-index: 300;

        li {
            display: block;
            margin: 0;
            float: none;

            &::before {
                content: '';
                padding: 0;
                margin: 0;
                border: 0;
            }

            &:hover {
                background-color: rgba(0, 0, 0, 0.125);
            }
        }

        a {
            padding: 0.75em 1em;
            line-height: 1.314;
        }
    }

    li:hover .menu__submenu {
        display: block;
    }
}

// Breadcrumb Nav
.breadcrumb {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 3em;
    color: transparent;

    a,
    span a.SkinObject {
        color: @color__brand-gold;
        font-size: 1.333em;
        padding: 0.25em;
        text-transform: capitalize;
    }

    span {
        display: flex;
        align-items: center;
    }

    .home-link {
        &::after {
            content: '»';
            margin-left: 0.25em;
            color: #555;
        }
    }

    span {
        &[itemprop='breadcrumb'] {
            span {
                &:first-child {
                    flex-wrap: wrap;
                }
            }
        }
    }

    #dnn_dnnBreadcrumb_lblBreadCrumb {
        a.SkinObject {
            &:last-child {
                font-weight: bold;
                color: @color__brand-dark-blue;
            }
        }
    }

    span[itemprop='breadcrumb'] {
        span[itemprop='itemListElement'] {
            a {
                position: relative;
                padding-right: 0.75em;

                &::after {
                    content: '»';
                    position: absolute;
                    right: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    color: #555;
                }
            }
        }
    }

    span[itemprop='itemListElement'] {
        &:last-child {
            span {
                font-weight: bold;
                color: @color__brand-dark-blue;
            }

            a {
                &::after {
                    display: none;
                }
            }
        }
    }
}

// Footer Nav Menu
.footer-nav {
    text-align: center;

    a {
        color: @color__white;
        font-size: 1.133em;
        padding: 0.75em 0.25em;

        &:hover {
            text-decoration: underline;
        }
    }
}

///////// Media Queries /////////

// 1x Small / Small Tablet, Large Phone
@media screen and (min-width: @breakpoint__1x-small) {
