// Small / Small Tablet
@media screen and (min-width: @breakpoint__small) {
}

// Mid-Size / Average Tablet
@media screen and (min-width: @breakpoint__medium) {
    nav {
        display: flex;
        //justify-content: flex-end;

        > ul {
            .horizontal-list();

            li::before {
                top: 25%;
                font-size: 1.333em;
            }

            li.menu-item.menu-item__back {
                display: none;
            }
        }
    }

    .header--main-nav {
        ul {
            li {
                &::before {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 0.25em;
                    left: 0;
                    width: 1px;
                    height: 1.75em;
                    background: #9dbacb;
                }

                a {
                    padding: 0.5em 0;
                }
            }
        }
    }

    .interior-nav {
        ul {
            li {
                float: left;

                &::before {
                    content: '';
                    position: absolute;
                    left: -3px;
                    border-left: 1px solid #477e9e;
                    width: 1px;
                    height: 43%;
                    top: 50%;
                    transform: translateY(-50%);
                }

                &:not(:first-child) {
                    border-top: none;
                }
            }
        }
    }
}

// Large / Desktop
@media screen and (min-width: (@breakpoint__large + 100px)) {
    .header--main-nav {
        ul {
            li {
                &::before {
                    top: 0.5em;
                }

                a {
                    &[data-level='0'] span {
                        font-size: 1.125em;
                    }
                }
            }
        }
    }
}

// 1x Large / Widescreen Desktop
@media screen and (min-width: (@breakpoint__1x-large + 100px)) {
    .header--main-nav {
        ul {
            li {
                a {
                    &[data-level='0'] span {
                        font-size: 1.5em;
                    }
                }
            }
        }
    }
}

// 2x Large / Widescreen Desktop, TV
@media screen and (min-width: @breakpoint__2x-large) {
}

// Print
@media print {
    nav {
        display: none; // hide all nav for print
    }
}
