ul.news--global-macro {
    list-style: disc;
    clear: both;

    li {
        color: #4d4d4d;
        margin-bottom: 0.5em;
    }

    a.global-macro--post {
        line-height: 1.3333;

        .post--title {
            color: #09537d;
            font-weight: 900;
            transition: color 0.2s;
        }

        .post--date {
            color: black !important;
            font-weight: 400 !important;
        }

        &:focus,
        &:hover {
            .post--title {
                color: #477e9e;
            }
        }

        &:focus {
            .post--title {
                text-decoration: underline;
            }
        }
    }

    & + a.global-macro--see-all {
        font-weight: 900;
        float: right;
        color: #09537d;
        transition: color 0.2s;

        &:focus,
        &:hover {
            color: #477e9e;
        }

        &:focus {
            text-decoration: underline;
        }
    }
}
