.site--footer {
    background-color: @color__brand-deep-blue;
    padding: 3em;
    color: @color__white;
    font-size: 14px;

    .container {
        .container(@site-width);

        .module-container {
            margin: 0 0 2em 0;
        }
    }

    .row {
        justify-content: space-between;
        padding-bottom: 1em;

        &.row__links {
            align-items: center;
        }

        .row--column {
            text-align: center;
            width: 100%;
        }
    }

    /// for Footer Nav Menus, see navigation.less
}

///////// Media Queries /////////

// 1x Small / Small Tablet, Large Phone
@media screen and (min-width: @breakpoint__1x-small) {
}

// Small / Small Tablet
@media screen and (min-width: @breakpoint__small) {
}

// Mid-Size / Average Tablet
@media screen and (min-width: @breakpoint__medium) {
    .site--footer {
        .row {
            .row--column {
                text-align: left;
                width: auto;
                min-width: 0;
            }

            &.row__addresses {
                .row--column {
                    min-width: 33%;
                }
            }
        }
    }
}

// Large / Desktop
@media screen and (min-width: @breakpoint__large) {
    .site--footer {
        .row {
            .row--column {
                text-align: left;
                width: auto;
                min-width: 0;
            }

            &.row__addresses {
                .row--column {
                    min-width: 0;
                }
            }
        }
    }
}

// 1x Large / Widescreen Desktop
@media screen and (min-width: @breakpoint__1x-large) {
    .site--footer {
        .row {
            &.row__addresses {
                .row--column {
                    max-width: 17%;
                }
            }
        }
    }
}

// 2x Large / Widescreen Desktop, TV
@media screen and (min-width: @breakpoint__2x-large) {
}

