.authorProfile {
    .clearfix();

    a {
        color: @color__brand-blue;
    }
    p {
        font-size: @font-size__base;
        line-height: @line-height__base;
    }

    .author-jobtitle {
        margin-bottom: 15px;
    }

    .author-contact-twitter {
        display: block;
        font-weight: bold;
        color: @color__brand-blue;
        margin-bottom: 15px;

        img {
            margin-right: 3px;
        }
    }

    .author-photo {
        float: right;
        overflow: hidden;
        border-radius: 50%;
        margin-left: 15px;

        img {
            width: auto !important;
            height: auto !important;
            max-width: 200px;
        }
    }

    .author-biography {
        font-size: @font-size__base;
        line-height: @line-height__base;

        p {
            font-size: @font-size__base;
            line-height: @line-height__base;
        }
    }
}
