.cta {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;

	.cta-box {
		flex: 1 1 280px;
		margin: 0 15px 2em 15px;
		padding: 2em 1em;
		position: relative;
		background-size: cover;

		.cta-box--content {
			background: rgba(255, 255, 255, 0.9);
			padding: 15px 5px;
			text-align: left;
			color: @color__brand-deep-blue;
			line-height: 1.314;

			h2 {
				font-size: 24px;
				margin-bottom: 20px;
			}

			&.text-center {
				text-align: center;

				h2 {
					text-align: center;
				}
			}
		}

		.cta-box--cta {
			position: absolute;
			bottom: -3em;
			left: 0;
			right: auto;
			width: 100%;
			text-align: center;

			a {
				background: @color__brand-gold;
				color: @color__white;
				padding: 0.5em 1.5em;
				border-radius: 18px;
				font-size: 14px;
				font-weight: bold;
			}
		}

		&.cta-box__alt {
			margin: 2em 15px 0 15px;
			padding: 2em 1em 6em 1em;

			.cta-box--content {
				padding: 15px;
				text-align: left;

				&.text-center {
					text-align: center;

					h2 {
						text-align: center;
					}
				}
			}

			.cta-box--cta {
				position: relative;
				text-align: center;
				top: 50px;
				bottom: auto;
			}
		}
	}
}

@media screen and (min-width: @breakpoint__medium) {
	.cta {
		.cta-box {
			padding: 3em 2em;

			.cta-box--content {
				h2 {
					font-size: 24px;
				}
			}

			&.cta-box__alt {
				padding: 3em 2em 5em 2em;

				.cta-box--cta {
					top: 40px;
				}
			}
		}
	}
}
