.site--main {
	.events {
		margin-bottom: 0;
		position: relative;

		.events--section-title {
			display: inline-block;
			margin-bottom: 15px;
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.section-title--calendar {
				flex: 1;
				text-align: center;

				a {
					color: #9a004d;
					transition: all 0.25s ease;
					text-transform: uppercase;

					&:hover {
						text-decoration: underline;
					}
				}
			}
			h2.section-title--title {
				flex: 1;
				text-transform: uppercase;
				font-size: @font-size__base * @font-factor__h3;
				line-height: 1.314;
				margin-top: 0;
				text-align: center;
				min-width: 100%;
			}
		}

		.events--feed {
			position: relative;
			display: flex;
			flex-wrap: wrap;
			margin: 0 -15px;

			.feed--event {
				background: @color__white;
				border: 2px solid @color__brand-blue;
				border-radius: 15px;
				padding: 15px;
				color: @color__brand-blue;
				margin-bottom: 20px;
				transition: all 0.05s ease;
				flex: 1;
				display: flex;
				margin: 0 15px 30px 15px;
				min-width: 270px;

				.event--image {
					max-width: 20%;
					min-width: 65px;
					margin-right: 15px;
					border-right: 1px solid #b4b6b8;
					overflow: hidden;
					-webkit-box-flex: 1;
					-webkit-flex: 1;
					-ms-flex: 1;
					flex: 1;
					position: relative;
					height: 100%;
					padding-right: 15px;

					.image--inner {
						width: 100%;
						height: 100%;
						position: relative;
						overflow: hidden;

						img {
							position: absolute;
							overflow: hidden;
							height: 100%;
							width: auto;
							max-width: none;
							top: 0;
							left: 0;
						}
					}
				}

				.event--content {
					flex: 1;
				}

				h3.event--title {
					font-size: @font-size__base * @font-factor__h5;
					line-height: 1.314;
					margin-top: 0;
				}

				.event--location {
					font-size: 80%;
					float: left;
					margin-right: 5px;
				}

				.event--date {
					display: block;
					padding-left: 5px;
					font-size: 80%;
					border-left: 1px solid @color__brand-blue;
					float: left;
				}

				&:hover {
					background-color: #004172;
					box-shadow: 7px 7px 9px 0px rgba(189, 190, 191, 0.75);
					color: @color__white;

					.event--image {
						border-right: 1px solid @color__brand-gold;
					}

					h3.event--title {
						color: @color__white;
					}

					.event--date {
						border-left: 1px solid @color__white;
					}
				}
			}
		}
	}
}

@media screen and (min-width: @breakpoint__medium) {
	.site--main {
		.events {
			.events--section-title {
				.section-title--calendar {
					min-width: 0;
					text-align: right;
				}
				h2.section-title--title {
					min-width: 0;
					text-align: left;
				}
			}
		}
	}
}
